
import {OldFormState, ModelState, DialogNewQuestionState, UiState, ApplicationState} from './ApplicationState';
import {AsyncAccount, AsyncTag, AsyncTopic} from './ApplicationState';
import {UiForm} from './ApplicationState';
import {AsyncQuestion, AsyncForm} from './ApplicationState';
import {LanguageLocale} from '../lib/lustroClientApi/LustroApiTypes';
import {LayoutItem} from '../lib/lustroClientApi/LustroApiTypes';

const defaultLanguage: LanguageLocale = 'en';

const initialState: ApplicationState = {
    updateCount: 0,
    displayLang: defaultLanguage,
    questions: getInitialModelState<AsyncQuestion>(),
    selectedAccountId: null,
    accounts: getInitialModelState<AsyncAccount>(),
    tags: getInitialModelState<AsyncTag>(),
    topics: getInitialModelState<AsyncTopic>(),
    filter: '',
    oldForm: getInitialOldFormState(),
    form: getInitialFormState(),
    uiState: getUiStateInitialState(),
    accountDialog: { filter: '' }
};


export function getInitialFormState(): AsyncForm {
    return undefined;
}

export function getInitialOldFormState(): OldFormState {
    return {
        questionIds: [-1],
        layouts: []
    };
}

export function getInitialUiFormState(): UiForm {
    return {
        formLayoutItems: [],
        topicsLayoutItems: {},
    };
}

export function getInitialModelState<T>(): ModelState<T> {
    return {
        _state: {
            didInvalidate: true,
            isFetching: false,
        },
        items: {},
    };
}

export function getDialogNewQuestionStateInitialState(): DialogNewQuestionState {
    return {name: '', type: 'open_ended', question_translations: []};
}

export function getUiStateInitialState(): UiState {
    return {
        uiModalState: null,
        dialogEditTagsState: {
            editingTagId: null,
        },
        dialogNewQuestionState: getDialogNewQuestionStateInitialState(),
        dialogNewTopicState: undefined,
        uiFormState: undefined,
    };
}

export function getInitialState(): ApplicationState {
    return initialState;
}



