/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import {LustroComponent} from './LustroComponent';
import {DialogNewTopicState} from '../reducers/ApplicationState';
import {LanguageLocale} from '../lib/lustroClientApi/LustroApiTypes';


interface DialogNewTopicProps {
    show: boolean;
    dialogNewTopicState: DialogNewTopicState;
    displayLang: LanguageLocale;
    onCancelCreateNewTopic: () => void;
    onSaveCreateNewTopic: () => void;
}
export class DialogNewTopic extends LustroComponent<DialogNewTopicProps> {
    render() {
        const {
            onCancelCreateNewTopic,
            show,
            onSaveCreateNewTopic,
            dialogNewTopicState,
            } = this.props;
        if (!show) {
            return null;
        }

        const onClickStopPropagation = (e) =>  {e.stopPropagation(); };

        const dialog = () => (
            <div className="modal fade in show" tabIndex={-1} role="dialog" onClick={onCancelCreateNewTopic}>
                <div className="modal-dialog" onClick={onClickStopPropagation}>
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" onClick={onCancelCreateNewTopic}>
                                <span>×</span>
                            </button>
                            <h4 className="modal-title">Create New Topic</h4>
                        </div>
                        <div className="modal-body">
                            <form className="selectable-question form-horizontal">
                                <div className="form-group">
                                    <label className="col-sm-2" htmlFor="new-question-name">Name:</label>
                                    <div className="col-sm-10">
                                        <input
                                            className="col-sm-12"
                                            id="new-question-name"
                                            type="text"
                                            autoFocus={true}
                                            placeholder="New Question Name"
                                            onChange={(e) => {
                                                const target = e.target as HTMLInputElement;
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="col-sm-2" htmlFor="new-question-text">Text:</label>
                                    <div className="col-sm-10">
                                        <input
                                            className="col-sm-12"
                                            id="new-question-text"
                                            type="text"
                                            placeholder="Fill in what you would like to ask."
                                            onChange={(e) => {
                                                return;
                                            }}
                                        />
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-default" onClick={onCancelCreateNewTopic}>
                                Cancel
                            </button>
                            <button type="button"className="btn btn-primary"
                                    onClick={onSaveCreateNewTopic}>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );

        const backDrop = () => (<div className="backdrop" onClick={onCancelCreateNewTopic}></div>);

        return (<div>{backDrop()}{dialog()}</div>);
    }
}



