import {Action} from './Action';

export function dispatchCollectionResults<T>(
    promise: Promise<T>, request: () => Action, received: (result: T) => Action) {
    return function (dispatch) {
        dispatch(request());
        return promise.then(
            result => dispatch(received(result))
        );
    };
}




