import {Form} from './LustroApiTypes';
import {ManagedEndpoint} from './ManagedEndpoint';
import {FormsEndpointQueryFilter} from './LustroApiTypes';
import {Account} from './LustroApiTypes';
import {FormType} from './LustroApiTypes';
import {updateWith} from '../ObjectUtils';
import {LanguageLocale} from './LustroApi';
/**
 * Created by paulo on 21/03/16.
 */

const defaultFormSettings: FormType = {
    account: null,
    form_topics: [],
    layout_items: [],
    required_language_locales: [],
    version: 1,
    canonical_language_locale: null,
    name_translations: [],
    is_published: false,
    is_primary: true,
    is_version_active: false,
    visibility: 'visible',
    primary: undefined,
};

export class FormsEndpoint extends ManagedEndpoint<Form, FormType, FormsEndpointQueryFilter> {
     createForm(account: Account | number, language: LanguageLocale, name: string) {
          const accountId: number = typeof account === 'number' ? account : account.id;
          return this.createItem<Form, FormType>(
              updateWith(defaultFormSettings, {
                   account: accountId,
                   canonical_language_locale: language,
                   name_translations: [{ language_locale: language, text: name }],
              })
          );
     }
}



