/// <reference path="../interfaces.d.ts"/>

import Redux = require('redux');
import { Actions, Action, DropQuestionOnPlaceHolderAction, isDropQuestionOnPlaceHolderAction } from '../actions/Actions';
import _ = require('lodash');
import {ChangeQuestionFilterPayload} from '../actions/Actions';
import {UiModalStates} from './ApplicationState';
import {UiForm} from './ApplicationState';
import {getInitialState} from './DefaultApplicationState';
import {getInitialUiFormState} from './DefaultApplicationState';
import {Account} from '../lib/lustroClientApi/lustroClientApi';
import {findAndReplaceOrAppend} from '../lib/ArrayUtils';
import {AccountsState} from './ApplicationState';
import {getUiStateInitialState} from './DefaultApplicationState';
import {DialogEditTagsState} from './ApplicationState';
import {TagsState} from './ApplicationState';
import {getInitialModelState} from './DefaultApplicationState';
import {AccountDialogState} from './ApplicationState';
import {Tag} from '../lib/lustroClientApi/lustroClientApi';
import {updateWith} from '../lib/Utils';
import {AsyncAccount} from './ApplicationState';
import {AsyncTag} from './ApplicationState';
import {UpdateAccountFilterPayload} from '../actions/Actions';
import {DialogNewQuestionState} from './ApplicationState';
import {QuestionsState} from './ApplicationState';
import {AsyncQuestion} from './ApplicationState';
import {TopicsState} from './ApplicationState';
import {AsyncTopic} from './ApplicationState';
import {BaseQuestion} from '../lib/lustroClientApi/LustroApiTypes';
import {Topic} from '../lib/lustroClientApi/LustroApiTypes';
import {isModifyAccountAction, isReceiveAccountsAction} from '../actions/Actions';
import {match} from '../lib/Utils';
import * as OldReducers from './OldReducers';

const {combineReducers} = Redux;

export const applyAction = combineReducers({
    updateCount,
    uiState: combineReducers({
        uiModalState: reduceUiModalState,
        dialogEditTagsState: reduceDialogEditTagsState,
        dialogNewQuestionState: reduceDialogNewQuestionState,
        uiFormState,
    }),
    oldForm: OldReducers.reduceForm,
    filter: handleChangeQuestionFilter,
    questions,
    displayLang: (s, a) : string => getInitialState().displayLang,
    accounts,
    selectedAccountId: account,
    tags,
    accountDialog,
});

function updateCount(state: number, action: Action) {
    return (state || 0) + 1;
}

function handleChangeQuestionFilter(state = '', action: Action): string {
    if (action.type !== Actions.CHANGE_QUESTION_FILTER) {
        return state;
    }

    return (action.payload as ChangeQuestionFilterPayload).filter;
}

export function reduceUiModalState(state: UiModalStates = null, action: Action): UiModalStates {
    const actionUiModalStateMapper: { [actionType: string]: UiModalStates } = {
        [Actions.OPEN_TAGS_DIALOG]: UiModalStates.editTags,
        [Actions.CLOSE_TAGS_DIALOG]: null,
        [Actions.OPEN_ACCOUNTS_DIALOG]: UiModalStates.selectAccount,
        [Actions.CLOSE_ACCOUNTS_DIALOG]: null,
        [Actions.OPEN_ACCOUNT_EDIT_DIALOG]: UiModalStates.editAccount,
        [Actions.CLOSE_ACCOUNT_EDIT_DIALOG]: null,
        [Actions.CREATE_QUESTION_DIALOG_OPEN]: UiModalStates.createNewQuestion,
        [Actions.CREATE_QUESTION_DIALOG_CANCEL]: null,
        [Actions.CREATE_QUESTION_DIALOG_SAVE]: null,
        [Actions.CREATE_TOPIC_DIALOG_OPEN]: UiModalStates.createNewTopic,
    };

    const nextUiModalState = actionUiModalStateMapper[action.type];
    return nextUiModalState === undefined ? state : nextUiModalState;
}

export function reduceDialogEditTagsState(state: DialogEditTagsState = { editingTagId: null },
                                          action: Action): DialogEditTagsState {
    switch (action.type) {
        case Actions.TAG_DIALOG_STOP_EDIT_TAG:
            return updateWith(state, { editingTagId : null });
        case Actions.TAG_DIALOG_EDIT_TAG:
            return updateWith(state, {editingTagId : action.payload});
        default:
            return state;
    }
}

export function reduceDialogNewQuestionState(
        state: DialogNewQuestionState = getUiStateInitialState().dialogNewQuestionState,
        action): DialogNewQuestionState {
    switch (action.type) {
        case Actions.NEW_QUESTION_DIALOG_CHANGE_NAME:
            return updateWith(state, {name: action.payload});
        case Actions.NEW_QUESTION_DIALOG_CHANGE_TYPE:
            return updateWith(state, {type: action.payload});
        case Actions.NEW_QUESTION_DIALOG_CHANGE_TRANSLATIONS: {
            return updateWith(state, {question_translations: [action.payload]});
        }
        default:
            return state;
    }
}

export function accounts(state: AccountsState = getInitialModelState<AsyncAccount>(), action: Action): AccountsState {
    return match(action.type, {
        [Actions.INVALIDATE_ACCOUNTS]: () => updateWith(state, {_state: updateWith(state._state, {didInvalidate : true})}),
        [Actions.REQUEST_ACCOUNTS]: () => updateWith(state, {_state: updateWith(state._state, {isFetching : true})}),
        [Actions.RECEIVE_ACCOUNTS]: () => (isReceiveAccountsAction(action) ? {
                _state: {
                    didInvalidate: false,
                    isFetching: false,
                },
                items: _(action.payload)
                    // convert Account in AsyncAccount
                    .map((account: Account) : AsyncAccount => updateWith(account, {_state: {didInvalidate: false, isFetching: false}}))
                    // Key by the account id
                    .keyBy(account => account.id)
                    .value()
            } : undefined),
        [Actions.MODIFY_ACCOUNT]: () => {
            if (isModifyAccountAction(action)) {
                const modifiedAccount = action.payload;
                const items = updateWith(state.items, { [modifiedAccount.id]: modifiedAccount} );
                return updateWith(state, {items});
            }
        }
    }, () => state);
}

export function account(state: number = null, action: Action) {
    switch (action.type) {
        case Actions.SELECT_ACCOUNT:
            return action.payload;
        default:
            return state;
    }
}

export function tags(state: TagsState = getInitialModelState<AsyncTag>(), action: Action): TagsState {
    switch (action.type) {
        case Actions.INVALIDATE_TAGS:
            return updateWith(state, {_state: updateWith(state._state, {didInvalidate : true})});
        case Actions.API_REQUEST_TAGS:
            return updateWith(state, {_state: updateWith(state._state, {isFetching : true})});
        case Actions.API_RECEIVE_TAGS:
            return {
                _state: {
                    didInvalidate: false,
                    isFetching: false,
                },
                items: _.keyBy( // key by tag id
                    _.map(  // converts Tag[] into AsyncTag[]
                        action.payload as Tag[],
                        (tag: Tag) : AsyncTag => _.assign<{}, Tag, {}, AsyncTag>(
                            {}, tag, {_state: {didInvalidate: false, isFetching: false}}
                        )
                    ),
                    tag => tag.id
                ),
            };
        case Actions.UPDATE_TAG:
            const updatedTag = action.payload as Tag;
            return updateWith(state, {items: updateWith(state.items, {[updatedTag.id]: updatedTag})});
        case Actions.APPEND_TAG:
            const newTag: Tag = action.payload as Tag;
            return updateWith(
                state, {items: updateWith(state.items, {[newTag.id]: newTag})}
            );
    }
    return state;
}

export function accountDialog(state: AccountDialogState = getInitialState().accountDialog, action: Action): AccountDialogState {
    const ACCOUNT_DIALOG_UPDATE_FILTER = (payload: UpdateAccountFilterPayload) => updateWith(state, { filter: payload.filter });

    const ActionHandlers = {
        ACCOUNT_DIALOG_UPDATE_FILTER,
    };

    const handler = ActionHandlers[action.type] || ((_: any) => state);
    return handler(action.payload);
}

export function questions(state: QuestionsState = getInitialModelState<AsyncQuestion>(), action): QuestionsState {
    switch (action.type) {
        case Actions.API_REQUEST_QUESTIONS:
            return updateWith(state, {_state: updateWith(state._state, {isFetching : true})});
        case Actions.INVALIDATE_QUESTIONS:
            return updateWith(state, {_state: updateWith(state._state, {didInvalidate : true})});
        case Actions.API_RECEIVE_QUESTIONS:
            return {
                _state: {
                    didInvalidate: false,
                    isFetching: false,
                },
                items: _.keyBy( // key by question id
                    _.map(  // converts BaseQuestion[] into AsyncQuestion[]
                        action.payload as BaseQuestion[],
                        (question: BaseQuestion) : AsyncQuestion => _.assign<{}, BaseQuestion, {}, AsyncQuestion>(
                            {}, question, {_state: {didInvalidate: false, isFetching: false}}
                        )
                    ),
                    question => question.id
                ),
            };
        default:
            return state;
    }
}

export function topics(state: TopicsState = getInitialModelState<AsyncTopic>(), action): TopicsState {
    switch (action.type) {
        case Actions.API_REQUEST_TOPICS:
            return updateWith(state, {_state: updateWith(state._state, {isFetching : true})});
        case Actions.API_RECEIVE_TOPICS:
            return {
                _state: {
                    didInvalidate: false,
                    isFetching: false,
                },
                items: _.keyBy( // key by question id
                    _.map(  // converts Topic[] into AsyncTopic[]
                        action.payload as Topic[],
                        (topic: Topic) : AsyncTopic => _.assign<{}, Topic, {}, AsyncTopic>(
                            {}, topic, {_state: {didInvalidate: false, isFetching: false}}
                        )
                    ),
                    topic => topic.id
                ),
            };
        default:
            return state;
    }
}

export function uiFormState(state: UiForm = getInitialUiFormState() , action: Action): UiForm {
    switch (action.type) {
        case Actions.UIFORM_DROP_QUESTION_ON_PLACEHOLDER:
            if (isDropQuestionOnPlaceHolderAction(action)) {
                const {topicId, topicLayoutItem} = action.payload;
                const newLayout = [
                    // replace the placeholder with the actual layout item
                    ...findAndReplaceOrAppend(
                           state.topicsLayoutItems[topicId],
                           topicLayoutItem,
                           (layoutItem) => layoutItem.id === '-1'
                    ),
                    // append the placeholder again
                    {id: '-1', visible_if: undefined}
                ];
                return updateWith(
                    state,
                    { topicsLayoutItems: updateWith(state.topicsLayoutItems, {[topicId]: newLayout}) }
                );
            }
            else return state;
        default:
            return state;
    }
}



