
const baseUrlTest = 'http://svc-lustro-form-test.revinate.com/api';
const baseUrlDev = 'http://svc-lustro-form-dev.revinate.com:8000/api';
const baseUrlProd = 'http://svc-lustro-form.revinate.com/api';

const defaultEnv = 'test';

export interface EndpointConstants {
    baseUrl: string;
    urlQuestions: string;
    urlTags: string;
    urlAccounts: string;
    urlForms: string;
    urlTopics: string;
}

export interface EndpointConfig {
    prod: EndpointConstants;
    test: EndpointConstants;
    dev: EndpointConstants;
}

export const serverEndpointConstants: EndpointConfig = {
    prod: {
        baseUrl: baseUrlProd,
        urlQuestions: baseUrlProd + '/questions.json',
        urlTags: baseUrlProd + '/tags.json',
        urlAccounts: baseUrlProd + '/accounts.json',
        urlForms: baseUrlProd + '/forms.json',
        urlTopics: baseUrlProd + '/topics.json',
    },
    test: {
        baseUrl: baseUrlTest,
        urlQuestions: baseUrlTest + '/questions.json',
        urlTags: baseUrlTest + '/tags.json',
        urlAccounts: baseUrlTest + '/accounts.json',
        urlForms: baseUrlTest + '/forms.json',
        urlTopics: baseUrlTest + '/topics.json',
    },
    dev: {
        baseUrl: baseUrlDev,
        urlQuestions: baseUrlDev + '/questions.json',
        urlTags: baseUrlDev + '/tags.json',
        urlAccounts: baseUrlDev + '/accounts.json',
        urlForms: baseUrlDev + '/forms.json',
        urlTopics: baseUrlDev + '/topics.json',
    },
};

export const defaultEndpointConstants: EndpointConstants = serverEndpointConstants[defaultEnv];



