/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import _ = require('lodash');
import {AsyncTopic} from '../reducers/ApplicationState';
import * as TopicHelper from '../lib/TopicHelper';
import {QuestionsState} from '../reducers/ApplicationState';
import {Question, QuestionPlaceHolder} from './Question';
import {LayoutItem} from '../lib/lustroClientApi/LustroApiTypes';

export interface TopicProps {
    topic: AsyncTopic;
    questions: QuestionsState;
    displayLang: string;
    topicsLayoutItems: Dictionary<LayoutItem[]>;
}

export function Topic(props: TopicProps) {
    const {topic} = props;
    return (
        <div className="panel panel-default">
            <div className="panel-heading">
                <h3 className="panel-title">{TopicHelper.getCanonicalName(topic)}</h3>
            </div>
            <div className="panel-body">
                <TopicLayoutItems {...props} />
            </div>
        </div>
    );
}

export function TopicLayoutItems(props: TopicProps) {
    const {topic, questions, displayLang, topicsLayoutItems} = props;
    const topicLayoutItems = topicsLayoutItems[topic.id];
    return (
        <div>
        {_.map(topicLayoutItems, (layoutElement) => (
            layoutElement.id !== '-1' ?
                (<Question
                     key={layoutElement.id}
                     question={questions.items[layoutElement.id]}
                     displayLang={displayLang} />) :
                 <QuestionPlaceHolder key={layoutElement.id} />
        ))}
        </div>
    );
}



