import { Actions, Action } from '../actions/Actions';
import _ = require('lodash');
import {InsertQuestionPayload} from '../actions/Actions';
import {MoveQuestionPayload} from '../actions/Actions';
import {ChangeQuestionFilterPayload} from '../actions/Actions';
import {RemoveQuestionFromFormPayload} from '../actions/Actions';
import {OldFormState} from './ApplicationState';
import {getInitialOldFormState} from './DefaultApplicationState';

const placeHolderId = -1;

function findPositionInQuestionIds(questionIds: LustroId[], questionId: LustroId) {
    return _.findIndex(questionIds, id => questionId === id);
}

function handleInsertQuestion(state: OldFormState = getInitialOldFormState(), action: Action): OldFormState {
    if (action.type !== Actions.INSERT_QUESTION) {
        return state;
    }

    const payload = action.payload as InsertQuestionPayload;
    const { questionId, beforeQuestionId } = payload;

    // Is it already in the form?
    if (findPositionInQuestionIds(state.questionIds, questionId) >= 0) {
        return state;  // yes, return.
    }

    const { questionIds } = state;
    let position = findPositionInQuestionIds(questionIds, beforeQuestionId);
    position = position < 0 ? questionIds.length : position;

    const newQuestionIds = [
        ...questionIds.slice(0, position),
        questionId,
        ...questionIds.slice(position)
    ];

    return {
        questionIds: movePlaceHolderToEndOfList(newQuestionIds),
        layouts: state.layouts
    };
}

export function handleMoveQuestion(state: OldFormState = getInitialOldFormState(), action: Action): OldFormState {
    if (action.type !== Actions.MOVE_QUESTION) {
        return state;
    }

    const payload = action.payload as MoveQuestionPayload;
    const { questionId, beforeQuestionId } = payload;
    const questionIds = state.questionIds;

    const currentPosition = _.findIndex(questionIds, id => questionId === id);
    const placeHolderId = -1;
    const qid = currentPosition < 0 ? placeHolderId : questionId;

    return {
        questionIds: moveRelativeQuestions(questionIds, qid, beforeQuestionId),
        layouts: state.layouts
    };
}

function moveRelativeQuestions(questionIds: LustroId[], questionId: LustroId, beforeQuestionId: LustroId) {
    if (questionId === beforeQuestionId) {
        return questionIds;
    }

    const currentPosition = _.findIndex(questionIds, id => questionId === id);
    const foundRelativePosition = _.findIndex(questionIds, id => beforeQuestionId === id);
    const isAfter = currentPosition > foundRelativePosition;
    const offset = isAfter ? 0 : 1;
    const questionIdsClean = _.filter(questionIds, id => id !== questionId);
    const position = _.findIndex(questionIdsClean, id => beforeQuestionId === id) + offset;

    return [
        ...questionIdsClean.slice(0, position),
        questionId,
        ...questionIdsClean.slice(position)
    ];
}

function movePlaceHolderToEndOfList(questionIds: LustroId[]) {
    return questionIds.filter(id => id !== placeHolderId).concat([placeHolderId]);
}

function handleCancelQuestionDrag(state: OldFormState = getInitialOldFormState(), action: Action): OldFormState {
    if (action.type !== Actions.CANCEL_QUESTION_DRAG) {
        return state;
    }

    return {
        questionIds: movePlaceHolderToEndOfList(state.questionIds),
        layouts: state.layouts
    };
}

function handleChangeQuestionFilter(state = '', action: Action): string {
    if (action.type !== Actions.CHANGE_QUESTION_FILTER) {
        return state;
    }

    return (action.payload as ChangeQuestionFilterPayload).filter;
}

function handleRemoveQuestionFromForm(state: OldFormState = getInitialOldFormState(), action: Action): OldFormState {
    if (action.type !== Actions.REMOVE_QUESTION_FROM_FORM) {
        return state;
    }

    const { questionId } = action.payload as RemoveQuestionFromFormPayload;
    return {
        questionIds: state.questionIds.filter(id => questionId !== id),
        layouts: state.layouts
    };
}

export function reduceForm(state: OldFormState = getInitialOldFormState(), action: Action): OldFormState {
    const formHandlers: List<(state: OldFormState, action: Action) => OldFormState> = [
        handleInsertQuestion,
        handleMoveQuestion,
        handleCancelQuestionDrag,
        handleRemoveQuestionFromForm,
    ];
    return _.reduce(formHandlers, ((state, fn) => fn(state, action)), state);
}





