import {Actions, Payload, Action} from './Action';

export interface InsertQuestionPayload extends Payload {
    questionId: number;
    beforeQuestionId?: number; // optional before question id
}

export function insertQuestion(payload: InsertQuestionPayload): Action {
    return { type: Actions.INSERT_QUESTION, payload };
}

export interface MoveQuestionPayload extends Payload {
    questionId: number;
    beforeQuestionId?: number; // optional before question id
}

export function moveQuestion(payload: MoveQuestionPayload): Action {
    return { type: Actions.MOVE_QUESTION, payload };
}

export function cancelQuestionDrag(): Action {
    return { type: Actions.CANCEL_QUESTION_DRAG, payload: {} };
}

export interface ChangeQuestionFilterPayload extends Payload {
    filter: string;
}
export function changeQuestionFilter(payload: ChangeQuestionFilterPayload): Action {
    return { type: Actions.CHANGE_QUESTION_FILTER, payload: payload};
}

export interface RemoveQuestionFromFormPayload extends Payload {
    questionId: number;
}
export function removeQuestionFromForm(payload: RemoveQuestionFromFormPayload): Action {
    return { type: Actions.REMOVE_QUESTION_FROM_FORM, payload};
}




