import {AsyncModel} from '../reducers/ApplicationState';

export function shouldFetchAsyncModel(stateElement: AsyncModel) {
    if (!stateElement) {
        return true;
    } else if (stateElement._state.isFetching) {
        return false;
    } else {
        return stateElement._state.didInvalidate;
    }
}




