import {Action} from './Actions';
import {Actions} from '../constants/Actions';
import {BaseQuestion} from '../lib/lustroClientApi/LustroApiTypes';

export function requestQuestions(): Action {
    return {type: Actions.API_REQUEST_QUESTIONS, payload: {}};
}

export function receiveQuestions(payload: BaseQuestion[]): Action {
    return {type: Actions.API_RECEIVE_QUESTIONS, payload: payload};
}

export function invalidateQuestions(): Action {
    return {type: Actions.INVALIDATE_QUESTIONS, payload: {}};
}



