/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import { FormLayout } from './FormLayout';
import { Form } from './Form';
import { QuestionSelectionList } from './QuestionSelectionList';
import { DialogNewQuestion } from './DialogNewQuestion';
import { DialogNewTopic } from './DialogNewTopic';
import { DialogSelectAccount } from './DialogSelectAccount';
import { DialogEditTags } from './DialogEditTags';
import { DialogEditAccount } from './DialogEditAccount';
import { UiModalStates } from '../reducers/ApplicationState';
import { Tag } from '../lib/lustroClientApi/lustroClientApi';
import { ApplicationState } from '../reducers/ApplicationState';
import {ApplicationEvents} from '../events/ApplicationEvents';
import {updateWith} from '../lib/Utils';
import {TagsModelItems} from '../reducers/ApplicationState';

export interface ContentProps extends ApplicationState {
    actions: ApplicationEvents;
}

export function Content(props: ContentProps) {
    const {
        questions,
        displayLang,
        filter,
        oldForm,
        uiState: {uiModalState, dialogEditTagsState, dialogNewQuestionState, dialogNewTopicState},
        actions,
        accounts,
        selectedAccountId,
        tags,
        accountDialog
     } = props;

    const filteredTags: TagsModelItems = selectedAccountId ?
        _(tags.items).filter((tag: Tag) => (tag.account === selectedAccountId)).value()
        : tags.items;

    return (
        <div id="content">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-8 col-sm-8 col-lg-8">
                        <Form
                            form={{
                                _state: {didInvalidate: false, isFetching: false},
                                account: 1,
                                form_topics: [
                                    {topic: 1},
                                    {topic: 2},
                                ],
                                layout_items: [],
                                required_language_locales: [],
                                version: 1,
                                canonical_language_locale: 'en',
                                name_translations: [
                                    {language_locale: 'en', text: 'test form'},
                                ],
                                is_published: false,
                                is_primary: true,
                                is_version_active: false,
                                visibility: 'visible',
                                primary: undefined,
                                _links: undefined,
                            }}
                            topics={{
                                _state: undefined,
                                items: {
                                    [1]: {
                                        _state: {didInvalidate: false, isFetching: false},
                                        primary: undefined,
                                        id: 1,
                                        account: 1,
                                        topic_labels: [],
                                        topic_questions: [
                                            {question: 1, question_option: {is_required: true, is_allowing_not_applicable: false}},
                                            {question: 2, question_option: {is_required: true, is_allowing_not_applicable: false}},
                                        ],
                                        layout_items: [],
                                        required_language_locales: [],
                                        version: 1,
                                        canonical_language_locale: 'en',
                                        name_translations: [
                                            {language_locale: 'en', text: 'test topic'}
                                        ],
                                        is_published: false,
                                        is_primary: true,
                                        is_version_active: false,
                                        visibility: 'visible',
                                        _links: undefined,
                                    },
                                    [2]: {
                                        _state: {didInvalidate: false, isFetching: false},
                                        primary: undefined,
                                        id: 2,
                                        account: 1,
                                        topic_labels: [],
                                        topic_questions: [
                                            {question: 1, question_option: {is_required: true, is_allowing_not_applicable: false}},
                                            {question: 2, question_option: {is_required: true, is_allowing_not_applicable: false}},
                                        ],
                                        layout_items: [],
                                        required_language_locales: [],
                                        version: 1,
                                        canonical_language_locale: 'en',
                                        name_translations: [
                                            {language_locale: 'en', text: 'test topic 2'}
                                        ],
                                        is_published: false,
                                        is_primary: true,
                                        is_version_active: false,
                                        visibility: 'visible',
                                        _links: undefined,
                                    }
                                }
                            }}
                            questions={{
                                _state: undefined,
                                items: {
                                    [1]: {
                                        _state: undefined,
                                        type: 'open_ended',
                                        account: 1,
                                        version: 1,
                                        question_help_translations: [],
                                        question_option: {
                                            is_required: true,
                                            is_allowing_not_applicable: false,
                                        },
                                        primary: 1,
                                        name_translations: [
                                            {language_locale: 'en', text: 'question name 1'},
                                            {language_locale: 'es', text: 'nombre de la pregunta 1'}
                                        ],
                                        question_translations: [
                                            {language_locale: 'en', text: 'question 1'},
                                            {language_locale: 'es', text: 'pregunta 1'}
                                        ],
                                        id: 1,
                                        canonical_language_locale: 'en',
                                        is_published: true,
                                        is_primary: true,
                                        is_version_active: true,
                                        visibility: 'visible',
                                        _links: null,
                                    },
                                    [2]: {
                                        _state: undefined,
                                        type: 'open_ended',
                                        account: 1,
                                        version: 1,
                                        question_help_translations: [],
                                        question_option: {
                                            is_required: true,
                                            is_allowing_not_applicable: false,
                                        },
                                        primary: 1,
                                        name_translations: [
                                            {language_locale: 'en', text: 'question name 2'},
                                            {language_locale: 'es', text: 'nombre de la pregunta 2'}
                                        ],
                                        question_translations: [
                                            {language_locale: 'en', text: 'question 2'},
                                            {language_locale: 'es', text: 'pregunta 2'}
                                        ],
                                        id: 2,
                                        canonical_language_locale: 'en',
                                        is_published: true,
                                        is_primary: true,
                                        is_version_active: true,
                                        visibility: 'visible',
                                        _links: null,
                                    }
                                }
                            }}
                            displayLang="en"
                            uiForm={{
                                formLayoutItems: undefined,
                                topicsLayoutItems: {
                                    [1]: [
                                        {id: '1', visible_if:undefined},
                                        {id: '2', visible_if:undefined},
                                        {id: '-1', visible_if:undefined},
                                    ],
                                    [2]: [
                                        {id: '1', visible_if:undefined},
                                        {id: '-1', visible_if:undefined},
                                        {id: '2', visible_if:undefined},
                                    ]
                                }
                            }}
                            {...actions}
                        />
                        <FormLayout {...props} {...actions}  questions={questions} lang={displayLang}/>
                    </div>
                    <div className="col-md-4 col-sm-4 col-lg-4">
                        <QuestionSelectionList
                            questions={questions}
                            filter={filter}
                            usedQuestionIds={oldForm.questionIds}
                            {...actions}
                        />
                    </div>
                </div>
                <DialogNewQuestion
                    show={uiModalState === UiModalStates.createNewQuestion}
                    dialogNewQuestionState={dialogNewQuestionState}
                    displayLang={displayLang}
                    {...actions}
                />
                <DialogNewTopic
                    show={uiModalState === UiModalStates.createNewTopic}
                    dialogNewTopicState={dialogNewTopicState}
                    displayLang={displayLang}
                    {...actions}
                />
                <DialogSelectAccount
                    show={uiModalState === UiModalStates.selectAccount}
                    accounts={accounts}
                    accountDialog={accountDialog}
                    {...actions}
                />
                <DialogEditTags
                    show={uiModalState === UiModalStates.editTags}
                    selectedAccountId={selectedAccountId}
                    tags={updateWith(tags, { items: filteredTags })}
                    dialogEditTagsState={dialogEditTagsState}
                    displayLang={displayLang}
                    {...actions}
                />
                <DialogEditAccount
                    show={uiModalState === UiModalStates.editAccount}
                    accounts={accounts.items}
                    selectedAccountId={selectedAccountId}
                    {...actions}
                />
            </div>
        </div>
    );
}



