/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import _ = require('lodash');
import {AsyncForm} from '../reducers/ApplicationState';
import {UiForm} from '../reducers/ApplicationState';
import {TopicsState} from '../reducers/ApplicationState';
import {QuestionsState} from '../reducers/ApplicationState';
import {Topic} from '../components/Topic';

export interface FormProps {
    uiForm: UiForm;
    form: AsyncForm;
    topics: TopicsState;
    questions: QuestionsState;
    displayLang: string;
    onCreateNewTopic: () => void;
}

export function Form(props: FormProps) {
    const { form, topics, questions, displayLang, uiForm, onCreateNewTopic } = props;
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="panel panel-default">
                    <div className="panel-heading">
                        <h2 className="panel-title">Form</h2>
                    </div>
                    <div className="panel-body form-layout">
                        <div className="lustro-survey-form-banner">
                            <image src="https://img.revinate.com/image/upload/d8ad18tquht6cmpgrcwl.jpg"/>
                        </div>
                        <div>
                            <button className="btn-sm btn-primary" onClick={onCreateNewTopic}>
                                Create New Topic
                            </button>
                        </div>
                        { _.map(form.form_topics, (formTopic) => (
                            <Topic
                                key={formTopic.topic}
                                topic={topics.items[formTopic.topic]}
                                questions={questions}
                                displayLang={displayLang}
                                topicsLayoutItems={uiForm.topicsLayoutItems} />
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}



