import {Action, Actions} from './Action';
import {ApplicationState} from '../reducers/ApplicationState';
import {defaultEndpointConstants} from '../constants/ServerEndpointConstants';
import {QuestionsEndpoint} from '../lib/lustroClientApi/QuestionsEndpoint';
import {DialogNewQuestionTypeMap} from '../reducers/ApplicationState';
import {BaseQuestion} from '../lib/lustroClientApi/LustroApiTypes';
import {receiveQuestions} from './QuestionsActions';
import {requestQuestions} from './QuestionsActions';
import {dispatchCollectionResults} from './ApiActions';
import {shouldFetchAsyncModel} from './ApiActionHelper';

const questionsUrl = defaultEndpointConstants.urlQuestions;
const questionsEndpoint = new QuestionsEndpoint(questionsUrl);

function createOpenEndedQuestion(accountId, translations, name) {
    return questionsEndpoint.createOpenEndedQuestion(
        accountId,
        translations[0].language_locale,
        name,
        translations[0].text
    );
}

export function createQuestion() {
    return function(dispatch, getState: () => ApplicationState) {
        const {
            selectedAccountId,
            uiState: { dialogNewQuestionState: { name, type, question_translations }}
            } = getState();
        const questionTypeToCreateFunctionMap: {[questionType: string]: (...args) => Promise<BaseQuestion>} = {
            [DialogNewQuestionTypeMap['Open Ended Question']]: createOpenEndedQuestion,
        };
        return questionTypeToCreateFunctionMap[type](selectedAccountId, question_translations, name)
            .then(question => dispatch(createQuestionDialogSave()));
    };
}

export function createQuestionDialogOpen(): Action {
    return { type: Actions.CREATE_QUESTION_DIALOG_OPEN, payload: {} };
}

export function createTopicDialogOpen(): Action {
    return { type: Actions.CREATE_TOPIC_DIALOG_OPEN, payload: {} };
}

export function createQuestionDialogCancel(): Action {
    return { type: Actions.CREATE_QUESTION_DIALOG_CANCEL, payload: {}};
}

export function createQuestionDialogSave(): Action {
    return {type: Actions.CREATE_QUESTION_DIALOG_SAVE, payload: {}};
}

export function fetchQuestions() {
    const result = questionsEndpoint
        .fetchCollection()
        .then(collection => collection._embedded.resources);
    return dispatchCollectionResults(result, requestQuestions, receiveQuestions);
}

export function fetchQuestionsIfNeeded() {
    return (dispatch, getState) => {
        const questions = getState().questions;
        if (shouldFetchAsyncModel(questions)) {
            return dispatch(fetchQuestions());
        } else {
            return Promise.resolve();
        }
    };
}



