import rest = require('rest');
import mime = require('rest/interceptor/mime');
import * as _ from 'lodash';
import {updateWith, defaultWith} from '../Utils';
import queryString = require('query-string');
import { QueryParams, QueryParamsDictionary } from './halTypes';
import { HalBody, HalCollectionBody } from './halTypes';

export * from './halTypes';

const restClient = rest.wrap(mime);
const restClientWrite = rest.wrap(mime);

export class ApiError extends Error {
    constructor(public response: rest.Response) {
        super(response.status.text);
    }
}

export function appendQueryParamsToPath(path: string, queryParams: QueryParams) {
    const parts = path.split('?', 2);
    const url = parts[0];
    const queryPart = parts[1] || '';
    const queryParamsFixArrays = _.mapKeys(queryParams as QueryParamsDictionary, (value, key) => {
        return value instanceof Array ? key.replace('[]', '') + '[]' : key;
    });
    const query = queryString.stringify(updateWith(queryString.parse(queryPart), queryParamsFixArrays));
    const params = query ? '?' + query : '';
    return url + params;
}


function makeRequest(path: string|rest.Request, queryParams: QueryParams = {}) {
    const baseRequest: rest.Request = typeof path === 'string' ? { path } : path;
    const isRead = baseRequest.method === undefined || baseRequest.method === 'GET';
    const client = isRead ? restClient : restClientWrite;

    const method = isRead ? 'GET' : 'POST';
    const methodOverrideHeaders = isRead ? {} : { 'X-HTTP-Method-Override' : baseRequest.method };
    const headers = updateWith(
        baseRequest.headers || {},
        methodOverrideHeaders
    );

    const requestSettings: rest.Request = { method, headers, path: appendQueryParamsToPath(baseRequest.path, queryParams) };
    const request = updateWith(baseRequest, requestSettings);

    return client(request).then(
        response => {
            if (response.status.code >= 300 && response.status.code < 306) {
                return makeRequest(response.headers['Location']);
            }
            if (response.status.code < 200 || response.status.code >= 300) {
                throw new ApiError(response);
            }
            return response.entity;
        },
        error => {
            // console.log('Error: ' + request.path);
            // console.log(error);
            return error;
        }
    );
}

const defaultQueryParam: QueryParams = {
    page: 1,
    limit: 200
};


export class HalCollectionEndPoint<T extends HalBody, U, F extends QueryParams> {

    constructor(public url: string) {}

    fetchCollection<X extends T>(queryParams?: F | string): Promise<HalCollectionBody<X>> {
        if (typeof queryParams === 'string') {
            return makeRequest(queryParams);
        }
        const query = defaultWith(queryParams as F || {}, defaultQueryParam);
        return makeRequest(this.url, query);
    }

    fetchItem<X extends T>(url: string): Promise<X> {
        return makeRequest(url);
    }

    updateItem<X extends T>(item: X, fields: {[index: string]: any}): Promise<X> {
        const data = updateWith(item, fields);
        return makeRequest({
            headers: { 'Content-Type': 'application/json' },
            path: item._links.self.href,
            method: 'PUT',
            entity: data
        });
    }

    createItem<X extends T, Y extends U>(item: Y, queryParams?: QueryParams): Promise<X> {
        return makeRequest(
            {
                headers: { 'Content-Type': 'application/json' },
                path: this.url,
                method: 'POST',
                entity: item
            },
            queryParams
        );
    }
}



