import {Dispatch} from 'redux';
import {AccountsEndpoint, AccountsEndpointQueryFilter} from '../lib/lustroClientApi/lustroClientApi';
import {ThunkInterface} from './Action';
import {EditAccountPayload, receiveAccounts, requestAccounts, editAccount} from './AccountsActions';
import {Account} from '../lib/lustroClientApi/lustroClientApi';
import {defaultEndpointConstants} from '../constants/ServerEndpointConstants';
import {dispatchCollectionResults} from './ApiActions';
import {shouldFetchAsyncModel} from './ApiActionHelper';

const accountsUrl = defaultEndpointConstants.urlAccounts;
const accountsEndpoint = new AccountsEndpoint(accountsUrl);
const accountQueryStream = accountsEndpoint.createQueryStream();

export function bindAccountSearchQueries(): ThunkInterface {
    return (dispatch: Dispatch) => {
        accountQueryStream.observable.subscribe((collection) => {
            dispatch(receiveAccounts(collection._embedded.resources));
        });
    };
}

export function queryAccounts(query: AccountsEndpointQueryFilter) {
    accountQueryStream.subject.onNext(query);
}


export function fetchAccounts(): ThunkInterface {
    const result = accountsEndpoint
        .fetchCollection()
        .then(collection => collection._embedded.resources);
    return dispatchCollectionResults(result, requestAccounts, receiveAccounts);
}

export function fetchAccountsIfNeeded(): ThunkInterface {
    return (dispatch, getState) => {
        const accounts = getState().accounts;
        if (shouldFetchAsyncModel(accounts)) {
            dispatch(fetchAccounts());
        }
    };
}

export function updateAccount(payload: EditAccountPayload): ThunkInterface {
    return function(dispatch, getState) {
        const account = _.find(
            getState().accounts.items,
            (account: Account) => account.id === payload.accountId
        );
        accountsEndpoint.updateItem(account, payload)
            .then((account) => dispatch(editAccount(account)));
    };
}



