import * as _ from 'lodash';
import {TopicType, Translation} from './lustroClientApi/lustroClientApi';

export function findNameTranslation(topic: TopicType, language: string): Translation  {
    return _.find(topic.name_translations, tran => tran.language_locale === language);
}

export function getCanonicalName(topic: TopicType): string  {
    return findNameTranslation(topic, topic.canonical_language_locale).text;
}




