import {Action} from './Action';
import {Actions} from '../constants/Actions';
import {fetchTagsIfNeeded} from './TagsApiActions';
import {Translation} from '../lib/lustroClientApi/LustroApi';
import {LayoutItem} from '../lib/lustroClientApi/LustroApiTypes';

export * from './AccountsActions';

export function openTagsDialog(payload = null): (dispatch) => void {
    return function (dispatch) {
        dispatch(fetchTagsIfNeeded());
        dispatch({ type: Actions.OPEN_TAGS_DIALOG, payload });
    };
}

export function editTag(tagId: number): Action {
    return { type: Actions.TAG_DIALOG_EDIT_TAG, payload: tagId };
}

export function editTagStop(): Action {
    return { type: Actions.TAG_DIALOG_STOP_EDIT_TAG, payload: {} };
}

export function closeTagsDialog(payload = null): Action {
    return { type: Actions.CLOSE_TAGS_DIALOG, payload };
}

export function changeNewQuestionName(name): Action {
    return { type: Actions.NEW_QUESTION_DIALOG_CHANGE_NAME, payload: name };
}

export function changeNewQuestionType(type): Action {
    return { type: Actions.NEW_QUESTION_DIALOG_CHANGE_TYPE, payload: type };
}

export function changeNewQuestionText(translation: Translation): Action {
    return { type: Actions.NEW_QUESTION_DIALOG_CHANGE_TRANSLATIONS, payload: translation };
}

export interface DropQuestionOnPlaceHolderAction extends Action {
    payload: {
        topicId: number;
        topicLayoutItem: LayoutItem;
    };
}
export function isDropQuestionOnPlaceHolderAction(action: Action): action is DropQuestionOnPlaceHolderAction {
    return action.type === Actions.UIFORM_DROP_QUESTION_ON_PLACEHOLDER;
}
export function dropQuestionOnPlaceHolder(topicId: number, topicLayoutItem: LayoutItem): DropQuestionOnPlaceHolderAction {
    return {
        type: Actions.UIFORM_DROP_QUESTION_ON_PLACEHOLDER,
        payload: {topicId, topicLayoutItem}
    };
}



