/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import {LustroComponent} from './LustroComponent';
import {NavBar} from './NavBar';
import {Content} from './Content';
import {ContentProps} from './Content';
import { DialogSelectAccount } from './DialogSelectAccount';
import { ExampleDialog } from './StdComponents';

export interface ApplicationWrapperProps extends ContentProps { }

export class ApplicationWrapper extends LustroComponent<ApplicationWrapperProps> {
    render() {
        if (this.props.selectedAccountId) {
            return (
                <div>
                    <NavBar {...this.props}/>
                    <Content {...this.props}/>
                </div>
            );
        }
        return (
            <div>
                <NavBar {...this.props}/>
                <DialogSelectAccount
                    show={true}
                    accounts={this.props.accounts}
                    accountDialog={this.props.accountDialog}
                    {...this.props.actions}
                    />
                <ExampleDialog />
            </div>
        );
    }
}



