import * as Actions from '../actions/Actions';
import {EditAccountPayload} from '../actions/Actions';
import Store = Redux.Store;
import {EditTagPayload} from '../actions/Actions';
import {CreateTagPayload} from '../actions/Actions';
import {Translation} from '../lib/lustroClientApi/LustroApi';


export declare interface ApplicationEvents {
    onCreateNewQuestion: () => void;
    onCreateNewTopic: () => void;
    onNewQuestionNameChange: (name: string) => void;
    onNewQuestionTypeChange: (type: string) => void;
    onNewQuestionQuestionTranslationChange: (questionTranslationPayload: Translation) => void;
    onCancelCreateNewQuestion: () => void;
    onCancelCreateNewTopic: () => void;
    onEditTagsCancel: () => void;
    onEditTagsOpen: () => void;
    onCreateNewTag: (payload: CreateTagPayload) => void;
    onEditTag: (tagId: number) => void;
    onEditTagDone: (editTagPayload: EditTagPayload) => void;
    onSaveCreateNewQuestion: () => void;
    onSaveCreateNewTopic: () => void;
    onInsertQuestion: (payload: Actions.InsertQuestionPayload) => void;
    onMoveQuestion: (payload: Actions.MoveQuestionPayload) => void;
    onCancelQuestionDrag: () => void;
    onChangeQuestionFilter: (payload: Actions.ChangeQuestionFilterPayload) => void;
    onRemoveQuestionFromForm: (payload: Actions.RemoveQuestionFromFormPayload) => void;
    onAccountsModalOpen: () => void;
    onAccountsModalClose: () => void;
    onAccountSelect: (accountId: number) => void;
    onAccountEditOpen: () => void;
    onAccountEditClose: () => void;
    onAccountEdit: (payload: EditAccountPayload) => void;
    onAccountUpdateFilter: (payload: Actions.UpdateAccountFilterPayload) => void;
    onAppInit: () => void;
}

export function getApplicationEvents(store: Store): ApplicationEvents {
    return {
        onCreateNewQuestion: () => store.dispatch(Actions.createQuestionDialogOpen()),
        onCreateNewTopic: () => store.dispatch(Actions.createTopicDialogOpen()),
        onNewQuestionNameChange: (name: string) => store.dispatch(Actions.changeNewQuestionName(name)),
        onNewQuestionTypeChange: (type: string) => store.dispatch(Actions.changeNewQuestionType(type)),
        onNewQuestionQuestionTranslationChange: (questionTranslationPayload: Translation) => store.dispatch(
            Actions.changeNewQuestionText(questionTranslationPayload)
        ),
        onEditTagsCancel: () => store.dispatch(Actions.closeTagsDialog()),
        onEditTagsOpen: () => store.dispatch(Actions.openTagsDialog()),
        onCreateNewTag: (payload: CreateTagPayload) => store.dispatch(Actions.createTag(payload)),
        onEditTag: (tagId: number) => store.dispatch(Actions.editTag(tagId)),
        onEditTagDone: (editTagPayload: EditTagPayload) => {
            editTagPayload !== null && store.dispatch(Actions.storeTagChanges(editTagPayload));
            store.dispatch(Actions.editTagStop());
        },
        onCancelCreateNewQuestion: () => store.dispatch(Actions.createQuestionDialogCancel()),
        onCancelCreateNewTopic: () => {},
        onSaveCreateNewQuestion: () => store.dispatch(Actions.createQuestion()),
        onSaveCreateNewTopic: () => {},
        onInsertQuestion: (payload: Actions.InsertQuestionPayload) => store.dispatch(Actions.insertQuestion(payload)),
        onMoveQuestion: (payload: Actions.MoveQuestionPayload) => store.dispatch(Actions.moveQuestion(payload)),
        onCancelQuestionDrag: () => store.dispatch(Actions.cancelQuestionDrag()),
        onChangeQuestionFilter: (payload: Actions.ChangeQuestionFilterPayload) => store.dispatch(
            Actions.changeQuestionFilter(payload)
        ),
        onRemoveQuestionFromForm: (payload: Actions.RemoveQuestionFromFormPayload) => store.dispatch(
            Actions.removeQuestionFromForm(payload)
        ),
        onAccountsModalOpen: () => store.dispatch(Actions.openAccountsDialog()),
        onAccountsModalClose: () => store.dispatch(Actions.closeAccountsDialog()),
        onAccountSelect: (accountId: number) => {
            store.dispatch(Actions.selectAccount(accountId));
            store.dispatch(Actions.closeAccountsDialog());
        },
        onAccountEditOpen: () => store.dispatch(Actions.openAccountEditDialog()),
        onAccountEditClose: () => store.dispatch(Actions.closeAccountEditDialog()),
        onAccountEdit: (payload: EditAccountPayload) => {
            store.dispatch(Actions.updateAccount(payload));
            store.dispatch(Actions.closeAccountEditDialog());
        },
        onAccountUpdateFilter: (payload: Actions.UpdateAccountFilterPayload) =>
            store.dispatch(Actions.changeAccountFilter(payload)),
        onAppInit: () => {
            store.dispatch(Actions.appInit());
            store.dispatch(Actions.fetchQuestionsIfNeeded());
        },
    };
}




