
import { Links, Link, HalBody } from './hal';
import * as LustroApi from './LustroApi';
import { QueryParams } from './hal';

export { Translation } from './LustroApi';
export { LanguageLocale } from './LustroApi';
export { LayoutItem } from './LustroApi';

export type QuestionType = LustroApi.Question;
export type AccountType = LustroApi.Account;
export type TagType = LustroApi.Tag;
export type FormType = LustroApi.Form;
export type TopicType = LustroApi.Topic;
export type OpenEndedQuestionType = LustroApi.OpenEndedQuestion;
export type MultipleChoiceQuestionType = LustroApi.MultipleChoiceQuestion;
export type RangeQuestionType = LustroApi.RangeQuestion;
export type PredefinedChoiceQuestionType = LustroApi.PredefinedChoiceQuestion;

export type CreateQuestionType = QuestionType;
export type CreateAccountType = AccountType;
export type CreateTagType = TagType;


export interface MultipleChoiceQuestion extends MultipleChoiceQuestionType, HalBody {}
export interface OpenEndedQuestion extends OpenEndedQuestionType, HalBody {}
export interface RangeQuestion extends RangeQuestionType, HalBody {}
export interface PredefinedChoiceQuestion extends PredefinedChoiceQuestionType, HalBody {}
export interface BaseQuestion extends LustroApi.BaseQuestion, HalBody {}
export interface Form extends LustroApi.Form, HalBody {}
export interface Topic extends LustroApi.Topic, HalBody {}
export interface Account extends AccountType, HalBody {
    _links: AccountLinks;
}
export interface Tag extends TagType, HalBody {}
export interface AccountLinks extends Links {
    tags?: Link;
    questions?: Link;
}

/**
 * Query Param Filters
 */
export interface AccountsEndpointQueryFilter extends QueryParams, LustroApi.AccountFilter {}
export interface TagsEndpointQueryFilter extends QueryParams, LustroApi.TagFilter {}
export interface QuestionsEndpointQueryFilter extends QueryParams, LustroApi.QuestionFilter {}
export interface FormsEndpointQueryFilter extends QueryParams, LustroApi.FormFilter {}
export interface TopicsEndpointQueryFilter extends QueryParams, LustroApi.TopicFilter {}

export const QuestionTypes = {
    open_ended: 'open_ended',
    multiple_choice: 'multiple_choice',
    range: 'range',
    predefined_choice: 'predefined_choice',
};




