import {Actions, Payload, Action} from './Action';
import * as Rx from 'rx';
import {TagsEndpoint} from '../lib/lustroClientApi/lustroClientApi';
import {dispatchCollectionResults} from './ApiActions';
import {Tag, LanguageLocale} from '../lib/lustroClientApi/lustroClientApi';
import {TagsState} from '../reducers/ApplicationState';
import {defaultEndpointConstants} from '../constants/ServerEndpointConstants';
import {shouldFetchAsyncModel} from './ApiActionHelper';

const tagsUrl = defaultEndpointConstants.urlTags;
const tagsEndpoint = new TagsEndpoint(tagsUrl);

export function fetchTags() {
    const result = tagsEndpoint
        .fetchCollection()
        .then(collection => collection._embedded.resources);
    return dispatchCollectionResults(result, requestTags, receiveTags);
}

export function fetchTagsIfNeeded() {
    return (dispatch, getState) => {
        const tags = getState().tags;
        if (shouldFetchAsyncModel(tags)) {
            return dispatch(fetchTags());
        } else {
            return Promise.resolve();
        }
    };
}

export function storeTagChanges(payload: EditTagPayload) {
    return function(dispatch, getState) {
        const tagState: TagsState = getState().tags;
        return Rx.Observable.from(_.values(tagState.items))
            .find((tag: Tag) => tag.id === payload.tagId)
            .map((tag: Tag) => tagsEndpoint.updateTranslation(
                tag,
                { language_locale: payload.locale, text: payload.text })
            )
            .flatMap(p => p)
            .map(tag => dispatch(updateTag(tag)))
            .toArray()
            .toPromise();
    };
}

export function createTag(payload: CreateTagPayload) {
    return function(dispatch, getState) {
        tagsEndpoint
            .createTag(payload.accountId, payload.language_locale, payload.text)
            .then(tag => dispatch(appendTag(tag)));
    };
}

export function invalidateTags(): Action {
    return { type: Actions.INVALIDATE_TAGS, payload: {} };
}

export function updateTag(modifiedTag): Action {
    return { type: Actions.UPDATE_TAG, payload: modifiedTag };
}

export interface CreateTagPayload extends Payload {
    accountId: number;
    text: string;
    language_locale: LanguageLocale;
    canonical_language_locale: LanguageLocale;
    visibility: string;
}

export function appendTag(tag: Tag): Action {
    return { type: Actions.APPEND_TAG, payload: tag };
}

export function requestTags(): Action {
    return { type: Actions.API_REQUEST_TAGS, payload: {} };
}

export function receiveTags(payload): Action {
    return { type: Actions.API_RECEIVE_TAGS, payload };
}

export interface EditTagPayload extends Payload {
    tagId: number;
    text: string;
    locale: LanguageLocale;
}



