
/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import { updateWith } from '../../lib/Utils';

interface ButtonProps {
    id?: string;                    // Optional id to add to the button element
    classNames?: string[];          // Optional class names to be added to the button
    onClick?: () => any;            // Optional on click handler
    size?: ButtonSizes;             // Optional size
    type?: ButtonTypes;             // Optional type
    children?: JSX.Element[];       // Passed in by React
}

export type ButtonSizes = 'xs' | 'extra-small' | 'sm' | 'small' | 'medium' | 'default' | 'lg' | 'large';
export type ButtonTypes = 'default' | 'primary' | 'secondary' | 'success' | 'danger' | 'info' | 'warning' | 'delete';

export function Button(props: ButtonProps = {}) {
    const { id, classNames= [], onClick= (() => {}), size, type } = props;
    const className = ['btn', buttonSizeToClass(size), buttonTypeToClass(type)].concat(classNames).join(' ');
    const buttonProps = {
        className,
        onClick
    };
    const idProp = id ? { id } : {};
    return (
        <button type="button" {...idProp} {...buttonProps}>{props.children}</button>
    );
}


export function ButtonPrimary(props: ButtonProps = {}) {
    const type: ButtonTypes = 'primary';
    const buttonProps = updateWith(props, { type });
    return (<Button {...buttonProps} />);
}


export function ExampleButton() {
    return (
        <ButtonPrimary>Ok</ButtonPrimary>
    );
}



const ButtonSizeToClass = {
    'xs': 'btn-xs',
    'extra-small': 'btn-xs',
    'sm': 'btn-sm',
    'small': 'btn-sm',
    'medium': '',
    'default': '',
    'lg': 'btn-lg',
    'large': 'btn-large'
};

const ButtonTypeToClass = {
    'default': 'btn-default',
    'primary': 'btn-primary',
    'secondary': 'btn-secondary',
    'success': 'btn-success',
    'danger': 'btn-danger',
    'info': 'btn-info',
    'warning': 'btn-warning',
    'delete': 'btn-delete'
};

function buttonSizeToClass(size: ButtonSizes) {
    return ButtonSizeToClass[size] || '';
}

function buttonTypeToClass(type: ButtonTypes) {
    return ButtonTypeToClass[type] || '';
}




