
/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import {LustroComponent} from './LustroComponent';
import {DialogNewQuestionState} from '../reducers/ApplicationState';
import {Translation, LanguageLocale} from '../lib/lustroClientApi/LustroApiTypes';
import {DialogNewQuestionTypeMap} from '../reducers/ApplicationState';


export class DialogNewQuestion extends LustroComponent<{
    show: boolean,
    dialogNewQuestionState: DialogNewQuestionState
    displayLang: LanguageLocale,
    onCancelCreateNewQuestion: () => void,
    onSaveCreateNewQuestion: () => void,
    onNewQuestionNameChange: (name: string) => void,
    onNewQuestionTypeChange: (type: string) => void,
    onNewQuestionQuestionTranslationChange: (questionTranslationPayload: Translation) => void,
}> {
    render() {
        const {
            onCancelCreateNewQuestion,
            show,
            onSaveCreateNewQuestion,
            dialogNewQuestionState,
            displayLang,
            onNewQuestionTypeChange,
            onNewQuestionQuestionTranslationChange,
            onNewQuestionNameChange
            } = this.props;
        if (!show) {
            return null;
        }

        const onClickStopPropagation = (e) =>  {e.stopPropagation(); };

        const dialog = () => (
            <div className="modal fade in show" tabIndex={-1} role="dialog" onClick={onCancelCreateNewQuestion}>
                <div className="modal-dialog" onClick={onClickStopPropagation}>
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" onClick={onCancelCreateNewQuestion}>
                                <span>×</span>
                            </button>
                            <h4 className="modal-title">Create New Question</h4>
                        </div>
                        <div className="modal-body">
                            <form className="selectable-question form-horizontal">
                                <div className="form-group">
                                    <label className="col-sm-2" htmlFor="new-question-name">Name:</label>
                                    <div className="col-sm-10">
                                        <input
                                            className="col-sm-12"
                                            id="new-question-name"
                                            type="text"
                                            autoFocus={true}
                                            placeholder="New Question Name"
                                            value={dialogNewQuestionState.name}
                                            onChange={(e) => {
                                                const target = e.target as HTMLInputElement;
                                                onNewQuestionNameChange(target.value);
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="col-sm-2" htmlFor="new-question-type-selector">Type:</label>
                                    <div className="col-sm-10">
                                        <select className="form-control" id="new-question-type-selector"
                                                value={dialogNewQuestionState.type}
                                                onChange={(e) => {
                                                    const target = e.target as HTMLSelectElement;
                                                    onNewQuestionTypeChange(target.value);
                                                }}
                                        >
                                            {_.map(DialogNewQuestionTypeMap, (type, text) => (
                                                    <option
                                                        key={type}
                                                        value={'' + type}>
                                                        {text}
                                                    </option>
                                                ))}
                                        </select>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="col-sm-2" htmlFor="new-question-text">Text:</label>
                                    <div className="col-sm-10">
                                        <input
                                            className="col-sm-12"
                                            id="new-question-text"
                                            type="text"
                                            placeholder="Fill in what you would like to ask."
                                            onChange={(e) => {
                                                const target = e.target as HTMLInputElement;
                                                onNewQuestionQuestionTranslationChange(
                                                    {language_locale: displayLang, text: target.value}
                                                );
                                            }}
                                        />
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-default" onClick={onCancelCreateNewQuestion}>
                                Cancel
                            </button>
                            <button type="button"className="btn btn-primary"
                                    onClick={onSaveCreateNewQuestion}>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );

        const backDrop = () => (<div className="backdrop" onClick={onCancelCreateNewQuestion}></div>);

        return (<div>{backDrop()}{dialog()}</div>);
    }
}

export default DialogNewQuestion;



