/// <reference path="../interfaces.d.ts"/>

import {Account, Tag, Topic, LanguageLocale} from '../lib/lustroClientApi/lustroClientApi';
import {Translation} from '../lib/lustroClientApi/LustroApi';
import {QuestionTypes} from '../lib/lustroClientApi/LustroApiTypes';
import {BaseQuestion} from '../lib/lustroClientApi/LustroApiTypes';
import {Form} from '../lib/lustroClientApi/LustroApiTypes';
import {LayoutItem} from '../lib/lustroClientApi/LustroApiTypes';

export const enum UiModalStates {
    createNewQuestion,
    createNewTopic,
    selectAccount,
    editAccount,
    editTags,
}

export interface FormQuestionLayout {
    questionId: number;

    // This is just for thought at the moment
    width?: number;     // number of columns
    symbol?: string;    // rating symbol
    rows?: number;      // comment box rows

}

export interface OldFormState {
    questionIds: number[];        // to be replaced by layouts
    layouts: FormQuestionLayout[];
}

export interface ModelState<T> extends AsyncModel {
    items: NumericDictionary<T>;
}

export interface AsyncModel {
    _state: {
        didInvalidate: boolean;
        isFetching: boolean;
    };
}


export type QuestionsModelItems = NumericDictionary<BaseQuestion>;
export interface AsyncQuestion extends BaseQuestion, AsyncModel {}
export interface QuestionsState extends ModelState<AsyncQuestion> {}

export type AccountsModelItems = NumericDictionary<Account>;
export interface AsyncAccount extends Account, AsyncModel {}
export interface AccountsState extends ModelState<AsyncAccount> {}
export interface AccountDialogState {
    filter: string;
}

export type TagsModelItems = NumericDictionary<Tag>;
export interface AsyncTag extends Tag, AsyncModel {}
export interface TagsState extends ModelState<AsyncTag> {}
export interface DialogEditTagsState {
    editingTagId: number;
}

export type TopicsModelItems = NumericDictionary<Topic>;
export interface AsyncTopic extends Topic, AsyncModel {}
export interface TopicsState extends ModelState<AsyncTopic> {}

export interface AsyncForm extends Form, AsyncModel {}

export const DialogNewQuestionTypeMap: Dictionary<string> =  {
    'Open Ended Question': QuestionTypes.open_ended,
    'Multiple Choice Question': QuestionTypes.multiple_choice,
    'Range Question': QuestionTypes.range,
    'Predefined Choice': QuestionTypes.predefined_choice,
};

export interface DialogNewQuestionState {
    name: string;
    type: string;
    question_translations: Translation[];
};

export interface DialogNewTopicState {
    name: string;
};

export interface UiForm {
    formLayoutItems: LayoutItem[];
    topicsLayoutItems: Dictionary<LayoutItem[]>;
};

export interface UiState {
    uiModalState: UiModalStates;
    dialogEditTagsState: DialogEditTagsState;
    dialogNewQuestionState: DialogNewQuestionState;
    dialogNewTopicState: DialogNewTopicState;
    uiFormState: UiForm;
}

export interface ApplicationState {
    updateCount: number;
    displayLang: LanguageLocale;
    questions: QuestionsState;
    selectedAccountId: number;
    accounts: AccountsState;
    tags: TagsState;
    topics: TopicsState;
    filter: string;
    oldForm: OldFormState;
    form: AsyncForm;
    uiState: UiState;
    accountDialog: AccountDialogState;
}



