import {Topic} from './LustroApiTypes';
import {ManagedEndpoint} from './ManagedEndpoint';
import {TopicsEndpointQueryFilter} from './LustroApiTypes';
import {Account} from './LustroApiTypes';
import {TopicType} from './LustroApiTypes';
import {updateWith} from '../ObjectUtils';
import {LanguageLocale} from './LustroApi';
/**
 * Created by paulo on 21/03/16.
 */

const defaultTopicSettings: TopicType = {
    primary: undefined,
    account: null,
    topic_labels: [],
    topic_questions: [],
    layout_items: [],
    required_language_locales: [],
    version: 1,
    canonical_language_locale: null,
    name_translations: [],
    is_published: false,
    is_primary: true,
    is_version_active: false,
    visibility: 'visible',
};

export class TopicsEndpoint extends ManagedEndpoint<Topic, TopicType, TopicsEndpointQueryFilter> {
     createTopic(account: Account | number, language: LanguageLocale, name: string) {
          const accountId: number = typeof account === 'number' ? account : account.id;
          return this.createItem<Topic, TopicType>(
              updateWith(defaultTopicSettings, {
                   account: accountId,
                   canonical_language_locale: language,
                   name_translations: [{ language_locale: language, text: name }],
              })
          );
     }
}



