import * as _ from 'lodash';
import { Account } from './LustroApiTypes';
import { BaseQuestion, QuestionType, QuestionsEndpointQueryFilter } from './LustroApiTypes';
import { OpenEndedQuestionType, OpenEndedQuestion, LanguageLocale } from './LustroApiTypes';
import { ManagedEndpoint } from './ManagedEndpoint';
import { updateWith } from '../Utils';

export { BaseQuestion, QuestionType, QuestionsEndpointQueryFilter } from './LustroApiTypes';
export { OpenEndedQuestionType, OpenEndedQuestion } from './LustroApiTypes';


/**
 * Endpoints
 */

const OPEN_ENDED: 'open_ended' = 'open_ended';

const defaultOpenEndedQuestionSettings: OpenEndedQuestionType  = {
    type: OPEN_ENDED,
    num_rows: null, // applies to text areas
    input_type: 'TextType',
    account: null,
    version: 1,
    question_option: {
        is_required: false,
        is_allowing_not_applicable: false,
    },
    name_translations: [],
    primary: null,
    question_translations: [],
    canonical_language_locale: null,
    question_help_translations: [],
    is_published: false,
    is_primary: true,
    is_version_active: false,
    visibility: 'visible',
    character_limit: null
};

export class QuestionsEndpoint extends ManagedEndpoint<BaseQuestion, QuestionType, QuestionsEndpointQueryFilter> {
    fetchQuestionsForAccount(account: number | Account, query: QuestionsEndpointQueryFilter = {}) {
        const accountId = typeof account === 'number' ? account : account.id;
        const accountQuery: QuestionsEndpointQueryFilter = { account: [accountId] };
        return this.fetchCollection(updateWith(query, accountQuery));
    }

    fetchOpenEndedQuestionsForAccount(account: number | Account, query: QuestionsEndpointQueryFilter = {}) {
        const accountId = typeof account === 'number' ? account : account.id;
        const customQuery: QuestionsEndpointQueryFilter = {
            account: [accountId],
            polymorphic_type: [OPEN_ENDED],
        };
        return this.fetchCollection<OpenEndedQuestion>(updateWith(query, customQuery));
    }

    findQuestionForAccount(
        account: number | Account,
        language: LanguageLocale,
        name: string,
        isExact = true,
        query: QuestionsEndpointQueryFilter = {}
    ) {
        const accountId = typeof account === 'number' ? account : account.id;
        const customQuery: QuestionsEndpointQueryFilter = {
            account: [accountId],
            canonical_language_locales: [language],
            name: name,
            is_exact_match: isExact,
        };
        return this.fetchCollection(updateWith(query, customQuery))
            .then( collection => _.first(collection._embedded.resources) );
    }

    findOpenEndedQuestionForAccount(
        account: number | Account,
        language: LanguageLocale,
        name: string,
        isExact = true,
        query: QuestionsEndpointQueryFilter = {}
    ) {
        const accountId = typeof account === 'number' ? account : account.id;
        const customQuery: QuestionsEndpointQueryFilter = {
            account: [accountId],
            canonical_language_locales: [language],
            name: name,
            is_exact_match: isExact,
            polymorphic_type: [OPEN_ENDED],
        };
        return this.fetchCollection(updateWith(query, customQuery))
            .then( collection => _.first(collection._embedded.resources) );
    }

    createOpenEndedQuestion(account: Account | number, language: LanguageLocale, name: string, text?: string) {
        const accountId: number = typeof account === 'number' ? account : account.id;
        const type = 'open_ended';
        return this.createItem<OpenEndedQuestion, OpenEndedQuestionType>(
            updateWith(defaultOpenEndedQuestionSettings, {
                account: accountId,
                canonical_language_locale: language,
                name_translations: [{ language_locale: language, text: name }],
                question_translations: [{ language_locale: language, text: text }]
            }),
            { question_type: type }
        );
    }

}




