import {Action, Payload, Actions} from './Action';
import {fetchAccountsIfNeeded, queryAccounts} from './AccountsApiActions';
import {Account} from '../lib/lustroClientApi/lustroClientApi';

export function openAccountsDialog(): (dispatch) => void {
    return function (dispatch) {
        dispatch(fetchAccountsIfNeeded());
        dispatch({ type: Actions.OPEN_ACCOUNTS_DIALOG, payload: {} });
    };
}

export function closeAccountsDialog(): Action {
    return { type: Actions.CLOSE_ACCOUNTS_DIALOG, payload: {} };
}

export function openAccountEditDialog(): Action {
    return { type: Actions.OPEN_ACCOUNT_EDIT_DIALOG, payload: {} };
}

export function closeAccountEditDialog(): Action {
    return { type: Actions.CLOSE_ACCOUNT_EDIT_DIALOG, payload: {} };
}

export function invalidateAccounts(): Action {
    return { type: Actions.INVALIDATE_ACCOUNTS, payload: {} };
}

export function requestAccounts(): Action {
    return { type: Actions.REQUEST_ACCOUNTS, payload: {} };
}

export interface ReceiveAccountsAction extends Action { payload: Account[]; }
export function isReceiveAccountsAction(action: Action): action is ReceiveAccountsAction {
    return action.type === Actions.RECEIVE_ACCOUNTS;
}
export function receiveAccounts(payload: Account[]): ReceiveAccountsAction {
    return { type: Actions.RECEIVE_ACCOUNTS, payload };
}

export interface UpdateAccountFilterPayload extends Payload {
    filter: string;
}

export function changeAccountFilter(payload: UpdateAccountFilterPayload) {
    return (dispatch) => {
        queryAccounts({
            name: payload.filter,
            limit: 20,
            page: 1
        });
        dispatch({ type: Actions.ACCOUNT_DIALOG_UPDATE_FILTER, payload });
    };
}

export interface SelectAccountAction extends Action { payload: number; }
export function selectAccount(accountId: number): SelectAccountAction {
    return { type: Actions.SELECT_ACCOUNT, payload: accountId };
}

export interface EditAccountPayload extends Payload {
    accountId: number;
    name: string;
}

export interface ModifyAccountAction extends Action { payload: Account; }
export function isModifyAccountAction(action: Action): action is ModifyAccountAction { return action.type === Actions.MODIFY_ACCOUNT; }
export function editAccount(payload: Account): ModifyAccountAction {
    return { type: Actions.MODIFY_ACCOUNT, payload };
}




