
/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import { ButtonPrimary } from './Button';

interface Props { children?: JSX.Element[]; }
export interface ModalProps extends Props { show: boolean; }
export function Modal(props: ModalProps) {
    const { show } = props;
    const modalContainerProps = {
        className: 'modal fade' + (show ? ' in' : ''),
        role: 'dialog',
        style: {display: show ? 'block' : 'none'}
    };
    return (
        <div {...modalContainerProps} >
            <div className="modal-dialog modal-lg">
                {/* Modal content*/}
                <div className="modal-content">
                    {props.children}
                </div>
            </div>
        </div>
    );
}

export interface HeaderProps extends Props {
    onCancel?: () => any;
}
export function Header(props: HeaderProps) {
    const { onCancel } = props;
    return (
        <div className="modal-header">
            { onCancel
                ? (<button type="button" className="close" data-dismiss="modal" onClick={onCancel}>×</button>)
                : null
            }
            <h4 className="modal-title">{props.children}</h4>
        </div>
    );
}

export interface BodyProps extends Props { }
export function Body(props: BodyProps) {
    return (
        <div className="modal-body">
            {props.children}
        </div>
    );
}

export interface FooterProps extends Props { }
export function Footer(props: FooterProps) {
    return (
        <div className="modal-footer">
            {props.children}
        </div>
    );
}

export function ExampleDialog() {
    const show = true;
    const onCancel = () => ({});
    return (
        <Modal show={show}>
            <Header onCancel={onCancel}>Dialog Title</Header>
            <Body>This is the body of the dialog.</Body>
            <Footer><ButtonPrimary>Ok</ButtonPrimary></Footer>
        </Modal>
    );
}




