import React = require('react');
import _ = require('lodash');

export function shouldComponentUpdate<P>(currentProps: P, nextProps: P) {
        return ! _.isMatchWith(currentProps, nextProps, (a, b) => a === b) || ! _.isMatchWith(nextProps, currentProps, (a, b) => a === b);
}

export class LustroComponent<P> extends React.Component<P, {}> {
    /* *
    shouldComponentUpdate(nextProps: P, nextState: {}, nextContext: any): boolean {
        return shouldComponentUpdate(this.props, nextProps);
    }
    /* */
}

export class DecoratedLustroComponent<P> extends LustroComponent<P> {
    // This declaration is needed for unit tests, to
    // reach out the original components when they are decorated.
    static DecoratedComponent;
}



