/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import {LustroComponent} from './LustroComponent';
import {AccountsState, AccountDialogState} from '../reducers/ApplicationState';
import {UpdateAccountFilterPayload} from '../actions/Actions';
import {AsyncAccount} from '../reducers/ApplicationState';
import * as _ from 'lodash';

export interface DialogSelectAccountProps {
    accounts: AccountsState;
    show: boolean;
    accountDialog: AccountDialogState;
    onAccountsModalClose: () => void;
    onAccountSelect: (accountId: number) => void;
    onAccountUpdateFilter: (payload: UpdateAccountFilterPayload) => void;
};

export class DialogSelectAccount extends LustroComponent<DialogSelectAccountProps> {
    render() {
        const { show, onAccountsModalClose, onAccountSelect, accounts, accountDialog, onAccountUpdateFilter } = this.props;
        if (!show) {
            return null;
        }

        const { filter } = accountDialog;
        /*
        const visibleAccounts = _.filter(
                _.map(accounts.items),
                (account: AsyncAccount) => (account.name.match(filter) || account.pid.match(filter))
            );
        */
        const visibleAccounts = accounts.items;
        const onClickStopPropagation = (e) =>  {e.stopPropagation(); };
        const dialog = () => (
            <div className="modal fade in show" tabIndex={-1} role="dialog" onClick={onAccountsModalClose}>
                <div className="modal-dialog" onClick={onClickStopPropagation}>
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" onClick={onAccountsModalClose}>
                                <span>×</span>
                            </button>
                            <h4 className="modal-title">Select Account</h4>
                        </div>
                        <div className="modal-body">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Search for..."
                                defaultValue={filter}
                                onChange={(e) => {
                                    const target = e.target as HTMLInputElement;
                                    onAccountUpdateFilter({ filter: target.value });
                                }}
                            />
                            <ul className="accounts-list">
                                {_.map(visibleAccounts, (account: AsyncAccount) => (
                                    <li key={account.id}>
                                        <a href={'#account-' + account.pid}
                                           onClick={() => onAccountSelect(account.id)}>
                                               {account.name}
                                        </a>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-default" onClick={onAccountsModalClose}>
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        );

        const backDrop = () => (<div className="backdrop" onClick={onAccountsModalClose}></div>);

        return (<div>{backDrop()}{dialog()}</div>);
    }
}



