/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import _ = require('lodash');
import {LustroComponent} from './LustroComponent';
import { DragSource, DragSourceConnector, DragSourceMonitor, ConnectDragSource } from 'react-dnd';
import { DragNDropTypes } from '../constants/DragNDrop';
import { icon } from './Icons';
import {IconTypes} from './Icons';
import {ChangeQuestionFilterPayload} from '../actions/Actions';
import {QuestionType} from '../lib/lustroClientApi/LustroApiTypes';
import * as QuestionHelper from '../lib/QuestionHelper';
import {QuestionTypes} from '../lib/lustroClientApi/LustroApiTypes';
import {QuestionsState} from '../reducers/ApplicationState';
import {DecoratedLustroComponent} from './LustroComponent';


export class QuestionSelectionList extends LustroComponent<{
    questions: QuestionsState;
    filter: string;
    usedQuestionIds: LustroId[];
    onCreateNewQuestion: () => void;
    onCancelQuestionDrag: () => void;
    onChangeQuestionFilter: (payload: ChangeQuestionFilterPayload) => void;
}> {


    question = (q: QuestionType) => (
        <SelectableQuestion key={q.id} question={q} onCancelQuestionDrag={this.props.onCancelQuestionDrag}/>
    );

    render() {
        const { questions, onCreateNewQuestion, usedQuestionIds, filter, onChangeQuestionFilter } = this.props;
        const questionIdsInUse = _.mapKeys(usedQuestionIds, _.identity);
        const validQuestions = _
            .filter(_.values(questions.items) as QuestionType[], (q) => (questionIdsInUse[q.id] === undefined))
            .filter(q => QuestionHelper.isVisible(q))
            .filter((q) => (!! QuestionHelper.getCanonicalName(q).match(filter)) );


        return (
            <div className="row">
                <h2>Question Selection List</h2>
                <div className="col-sm-12">
                    <div className="input-group">
                        <input
                            type="text"
                            className="form-control"
                            placeholder="Search for..."
                            value={filter}
                            onChange={(e) => {
                            const target = e.target as HTMLInputElement;
                            onChangeQuestionFilter({ filter: target.value });
                        }}
                        />
                        <span className="input-group-btn">
                            <button
                                className="btn btn-default"
                                type="button"
                                title="clear search"
                                onClick={() =>  onChangeQuestionFilter({ filter: ''}) }
                            >X</button>
                        </span>
                        <span className="input-group-btn">
                            <button
                                className="btn btn-primary"
                                type="button"
                                title="create new question"
                                onClick={onCreateNewQuestion}
                            >New</button>
                        </span>
                    </div>
                </div>
                <ul className="col-sm-12 question-selection-list list-group">
                    {validQuestions.map(this.question)}
                    <li className="list-group-item" key="newQ">
                        <button className="btn-sm btn-primary" onClick={onCreateNewQuestion}>
                            Create New Question
                        </button>
                    </li>
                </ul>
            </div>
        );
    }
}

interface QuestionProps {
    key?: number;
    question: QuestionType;
    isDragging?: boolean;
    connectDragSource?: ConnectDragSource;
    onCancelQuestionDrag: () => void;
}

const dragSpec = {
    beginDrag: (props: QuestionProps, monitor: DragSourceMonitor, component: SelectableQuestion) => {
        return props.question;
    },
    endDrag: (props: QuestionProps, monitor: DragSourceMonitor, component: SelectableQuestion) => {
        if (! monitor.didDrop()) {
            // We need to cancel any effect on the target.
            props.onCancelQuestionDrag();
        }
    }
};

function collect(connect: DragSourceConnector, monitor: DragSourceMonitor) {
    return {
        // Call this function inside render()
        // to let React DnD handle the drag events:
        connectDragSource: connect.dragSource(),
        // You can ask the monitor about the current drag state:
        isDragging: monitor.isDragging()
    };
}

@DragSource(DragNDropTypes.FORM_QUESTION, dragSpec, collect)
export class SelectableQuestion extends DecoratedLustroComponent<QuestionProps> {
    render() {
        const style = {
            cursor: 'move'
        };
        const { question, connectDragSource, isDragging } = this.props;
        return connectDragSource(
            <li style={style} className={'list-group-item' + (isDragging ? ' is-dragging' : '')} key={question.id}>
                <div className="selectable-question row">
                    <span className="col-sm-2">Name:</span>
                    <span className="col-sm-10">{QuestionHelper.getCanonicalName(question)}</span>
                    <span className="col-sm-2">Type:</span>
                    <span className="col-sm-10">{renderQuestionType(question)}</span>
                    <span className="col-sm-2">Text:</span>
                    <span className="col-sm-10">{renderQuestionText(question)}</span>
                </div>
            </li>
        );
    }
}

function renderQuestionType(question: QuestionType) {
    const questionType = question.type;
    switch (question.type) {
        // case QuestionTypes.rating: return renderRatingQuestionType(question as RatingQuestion);
        case QuestionTypes.open_ended: return (<span>{questionType} {icon(IconTypes.edit)}</span>);
        case QuestionTypes.multiple_choice: return (<span>{questionType} {icon(IconTypes.check)}</span>);
        // case QuestionTypes.yes_no: return (<span>{questionType} {icon(IconTypes.ok)}/{icon(IconTypes.remove)}</span>);
    }

    return (<span>{question.type}</span>);
}


function renderQuestionText(question: QuestionType) {
    const text = QuestionHelper.getCanonicalText(question);
    return (<span>{text}</span>);
}



