import * as LustroApi from './LustroApi';
import {findAndReplaceOrAppend} from '../Utils';

import { Account, LanguageLocale } from './LustroApiTypes';
import { Tag, TagType, TagsEndpointQueryFilter } from './LustroApiTypes';
import { ManagedEndpoint } from './ManagedEndpoint';

export { Tag, TagType, TagsEndpointQueryFilter } from './LustroApiTypes';

/**
 * Endpoints
 */

export class TagsEndpoint extends ManagedEndpoint<Tag, TagType, TagsEndpointQueryFilter> {

    fetchTagsForAccount(account: Account | number) {
        const accountId = typeof account === 'number' ? account : account.id;
        return this.fetchCollection({ account: [accountId] });
    }

    findTagForAccount(account: Account, language: LanguageLocale, name: string, isExact = true) {
        return this.fetchCollection({
            account: [account.id],
            canonical_language_locales: [language],
            name: name,
            is_exact_match: isExact
        });
    }

    createTag(accountId: number, language: LanguageLocale, text: string) {
        const tag: TagType = {
            account: accountId,
            canonical_language_locale: language,
            name_translations: [{ language_locale: language, text }],
            version: 1,
            visibility: 'visible'
        };
        return this.createItem(tag);
    }

    updateTranslation(tag: Tag, translation: LustroApi.Translation) {
        const name_translations = findAndReplaceOrAppend(
            tag.name_translations,
            translation,
            (tran: LustroApi.Translation) => tran.language_locale === translation.language_locale
        );
        return this.updateItem(tag, { name_translations });
    }
}



