
import { HalBody, HalCollectionBody, HalCollectionEndPoint } from './hal';
import { QueryParams } from './hal';
import * as Rx from 'rx';
import * as _ from 'lodash';

interface Dictionary<T> {
    [id: number]: T;
}

export class ManagedEndpoint<T extends HalBody, U, F extends QueryParams> implements HalCollectionEndPoint<T, U, F> {
    protected endpoint: HalCollectionEndPoint<T, U, F>;
    protected queryHashFields = ['page', 'limit'];
    protected subject = new Rx.Subject<T>();
    protected sharedSubject = this.subject.share();

    constructor(public url: string) {
        this.endpoint = new HalCollectionEndPoint<T, U, F>(url);
    }

    attachToObservable<X extends T>(observable: Rx.Observable<F>): Rx.Observable<HalCollectionBody<X>> {
        return observable
            .debounce(200)
            .flatMapLatest(queryParams => this.endpoint.fetchCollection<X>(queryParams))
            .share();
    }

    createQueryStream<X extends T>() {
        const subject = new Rx.ReplaySubject<F>(1);
        const observable = this.attachToObservable<X>(subject);
        return {
            subject,
            observable,
            query: (query: F) => {
                subject.onNext(query);
                return observable;
            }
        };

    }

    tap(predicate: (item: T) => void = p => p) {
        return this.sharedSubject.tap(predicate);
    }

    fetchCollection<X extends T>(queryParams?: F | string): Promise<HalCollectionBody<X>> {
        return this.endpoint.fetchCollection(queryParams).then(collection => {
            _.forEach(collection._embedded.resources, item => this.subject.onNext(item));
            return collection;
        });
    }

    fetchItem<X extends T>(url: string): Promise<X> {
        return this.endpoint.fetchItem(url).then(item => {
            this.subject.onNext(item);
            return item;
        });
    }

    updateItem<X extends T>(item: X, fields: {[index: string]: any}): Promise<X> {
        return this.endpoint.updateItem(item, fields).then(item => {
            this.subject.onNext(item);
            return item;
        });
    }

    createItem<X extends T, Y extends U>(item: Y, queryParams?: QueryParams): Promise<X> {
        return this.endpoint.createItem(item, queryParams).then(item => {
            this.subject.onNext(item);
            return item;
        });
    }
}


