
import * as _ from 'lodash';

/**
 * returns a new object based upon source with the fields updated from updateWith
 */
export function updateWith<T, U>(source: T, updateWith: U): T & U {
    return _.assign({}, source, updateWith) as T & U;
}

/**
 * returns a new object based upon source and fills in any missing fields with defaultWith
 */
export function defaultWith<T, U>(source: T, defaultWith: U): T & U {
    return _.defaults({}, source, defaultWith) as T & U;
}

/**
 * keeping it here for now in case we need a more flexible match logic which doesnt care about types
 * Ex:
 *
 *    res = flexibleMatch(
 *        'action 1',
 *        ['action 1', () => { return doStuffForAction1(); }],
 *        ['action 2', () => { return doStuffForAction2(); }]
 *    );
 */
export function flexibleMatch(source, ...args: [any, any][]) {
    const index = _.findIndex(args, (t) => t[0] === source);
    if (typeof args[index][1] === 'function') {
        return args[index][1]();
    }
    return args[index][1];
}

export function match<U>(pattern: string | number,
                         lookupTable: Dictionary<U | (() => U)>,
                         defaultFunction?: U | (() => U)): U {
    const isFnU = (f): f is () => U => typeof f === 'function';
    const found = lookupTable[pattern] === undefined ? defaultFunction : lookupTable[pattern];
    if (isFnU(found)) {
        return found();
    }
    return found as U;
}



