
import * as _ from 'lodash';
import { QuestionTypes, QuestionType, Translation, BaseQuestion } from './lustroClientApi/lustroClientApi';
import {OpenEndedQuestion} from './lustroClientApi/lustroClientApi';
import {MultipleChoiceQuestion} from './lustroClientApi/lustroClientApi';
import {RangeQuestion} from './lustroClientApi/lustroClientApi';
import {PredefinedChoiceQuestion} from './lustroClientApi/lustroClientApi';

export function findNameTranslation(question: QuestionType, language: string): Translation  {
    return _.find(question.name_translations, tran => tran.language_locale === language);
}

export function findTextTranslation(question: QuestionType, language: string): Translation  {
    return _.find(question.question_translations, tran => tran.language_locale === language);
}

export function getCanonicalName(question: QuestionType): string  {
    return findNameTranslation(question, question.canonical_language_locale).text;
}

export function getCanonicalText(question: QuestionType): string  {
    return findTextTranslation(question, question.canonical_language_locale).text;
}

export function isVisible(question: QuestionType): boolean  {
    return question.visibility === 'visible';
}

type Q = BaseQuestion;
export function isOpenEndedQuestion(q: Q): q is OpenEndedQuestion {
    return q.type === QuestionTypes.open_ended;
}
export function isMultipleChoiceQuestion(q: Q): q is MultipleChoiceQuestion {
    return q.type === QuestionTypes.multiple_choice;
}
export function isRangeQuestion(q: Q): q is RangeQuestion {
    return q.type === QuestionTypes.range;
}
export function isPredefinedChoiceQuestion(q: Q): q is PredefinedChoiceQuestion {
    return q.type === QuestionTypes.predefined_choice;
}




