/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import ReactDOM = require('react-dom');
import Redux = require('redux');
import Reducer = Redux.Reducer;
import { applyAction } from './reducers/ApplicationReducers';
import { DragDropContext } from 'react-dnd';
import HTML5Backend = require('react-dnd-html5-backend');
import { DevTools } from './components/DevTools';
import {ApplicationWrapper, ApplicationWrapperProps} from './components/ApplicationWrapper';
import * as thunkMiddleware from 'redux-thunk';
import {getApplicationEvents} from './events/ApplicationEvents';
import {bindAccountSearchQueries} from './actions/AccountsApiActions';
import {fetchAccountsIfNeeded} from './actions/Actions';


const App = DragDropContext<ApplicationWrapperProps>(HTML5Backend)(ApplicationWrapper);

const store = Redux.createStore(
    applyAction,
    Redux.compose(
        Redux.applyMiddleware(
            thunkMiddleware
        ),
        DevTools.instrument()
    )
);

const actions = getApplicationEvents(store);

function render() {
    const state = store.getState();
    const rootElement = document.getElementById('app');
    ReactDOM.render(
        <div>
            <App {...state} actions={actions} />
            <DevTools store={store}/>
        </div>,
        rootElement
    );
}

// Link the renderer to the store.
store.subscribe(render);
// Force the initial state and cause a render to happen.
actions.onAppInit();
store.dispatch(bindAccountSearchQueries());
store.dispatch(fetchAccountsIfNeeded());



