import {QuestionsState} from '../reducers/ApplicationState';
import {match} from '../lib/ObjectUtils';
import {QuestionTypes} from '../lib/lustroClientApi/LustroApiTypes';
import {AsyncQuestion} from '../reducers/ApplicationState';
import {OpenEndedQuestion} from '../lib/lustroClientApi/LustroApi';

export interface FormLayoutProps extends ApplicationEvents {
    lang: LanguageLocale;
    questions: QuestionsState;
    oldForm: {
        questionIds: number[],
    };
    onRemoveQuestionFromForm: (payload: Actions.RemoveQuestionFromFormPayload) => void;
}

export class FormLayout extends LustroComponent<FormLayoutProps> {
    render() {
        const { oldForm, lang } = this.props;
        const questions = _(oldForm.questionIds)
            .map(questionId => this.props.questions.items[questionId])
            .value();

        return (
            <div className="row">
                <div className="col-sm-12">
                    <div className="panel panel-default">
                        <div className="panel-heading">
                            <h2 className="panel-title">Form</h2>
                        </div>
                        <div className="panel-body form-layout">
                            <div className="lustro-survey-form-banner">
                                <image src="https://img.revinate.com/image/upload/d8ad18tquht6cmpgrcwl.jpg"/>
                            </div>
                            <FormQuestionsList
                                {...this.props}
                                lang={lang}
                                questions={questions}
                            />
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

interface FormQuestionsListProps {
    lang: LanguageLocale;
    questions: AsyncQuestion[];
    onInsertQuestion: (payload: Actions.InsertQuestionPayload) => void;
    onMoveQuestion: (payload: Actions.MoveQuestionPayload) => void;
    onRemoveQuestionFromForm: (payload: Actions.RemoveQuestionFromFormPayload) => void;
}

export function FormQuestionsList(props: FormQuestionsListProps) {
    const { questions, onInsertQuestion, onMoveQuestion, lang, onRemoveQuestionFromForm } = props;
    const formQuestion = (question) => {
        if (! question) {
            return (
                <FormQuestionPlaceHolder
                    key={-1}
                    questionId={-1}
                    onInsertQuestion={onInsertQuestion}
                    onMoveQuestion={onMoveQuestion}
                />
            );
        }
        const q = question;
        return (
            <FormQuestion
                key={q.id}
                lang={lang}
                question={q}
                onInsertQuestion={onInsertQuestion}
                onMoveQuestion={onMoveQuestion}
                onRemoveQuestionFromForm={onRemoveQuestionFromForm}
            />
        );
    };

    return (
        <div>
            {questions.map(formQuestion)}
        </div>
    );
}

interface FormQuestionProps {
    key?: number;
    question: QuestionType;
    lang: LanguageLocale;
    connectDropTarget?: ConnectDropTarget;
    connectDragSource?: ConnectDragSource;
    isDragging?: boolean;
    onInsertQuestion: (payload: Actions.InsertQuestionPayload) => void;
    onMoveQuestion: (payload: Actions.MoveQuestionPayload) => void;
    onRemoveQuestionFromForm: (payload: Actions.RemoveQuestionFromFormPayload) => void;
}


const dropSpec = {
    drop: (props: FormQuestionProps, monitor: DropTargetMonitor, component: FormQuestion) => {
        if (monitor.didDrop()) {
            // nested target already handled drop
            return;
        }

        // Obtain the dragged item
        const item = monitor.getItem() as QuestionType;

        props.onInsertQuestion({questionId: item.id, beforeQuestionId: props.question.id});
        props.onInsertQuestion({questionId: -1});
    },
    hover: (props: FormQuestionProps, monitor: DropTargetMonitor, component: FormQuestion) => {
        const item = monitor.getItem() as QuestionType;
        const { question } = props;
        if (question.id < 0 || item.id === question.id) {
            return;
        }
        props.onMoveQuestion({questionId: item.id, beforeQuestionId: question.id});
    }
};


function collectDrop(connect: DropTargetConnector, monitor: DropTargetMonitor) {
    return {
        // Call this function inside render()
        // to let React DnD handle the drag events:
        connectDropTarget: connect.dropTarget(),
        // You can ask the monitor about the current drag state:
        isOver: monitor.isOver(),
        isOverCurrent: monitor.isOver({ shallow: true }),
        canDrop: monitor.canDrop(),
        itemType: monitor.getItemType()
    };
}

const dragSpec: ReactDND.DragSourceSpec<FormQuestionProps> = {
    beginDrag: (props: FormQuestionProps, monitor: DragSourceMonitor, component: FormQuestion) => {
        return props.question;
    },
    endDrag: (props: FormQuestionProps, monitor: DragSourceMonitor, component: FormQuestion) => {
        if (! monitor.didDrop()) {
            // We need to cancel any effect on the target.
            props.onRemoveQuestionFromForm({ questionId: props.question.id});
        }
    }
};

function collectDrag(connect: DragSourceConnector, monitor: DragSourceMonitor) {
    return {
        // Call this function inside render()
        // to let React DnD handle the drag events:
        connectDragSource: connect.dragSource(),
        // You can ask the monitor about the current drag state:
        isDragging: monitor.isDragging()
    };
}

@ReactDND.DragSource(DragNDropTypes.FORM_QUESTION, dragSpec, collectDrag)
@ReactDND.DropTarget(DragNDropTypes.FORM_QUESTION, dropSpec, collectDrop)
export class FormQuestion extends DecoratedLustroComponent<FormQuestionProps> {
    render() {
        const { question, connectDropTarget, connectDragSource, isDragging } = this.props;
        return connectDropTarget(connectDragSource(
            <div className={'lustro-survey-form-question' + (isDragging ? ' is-dragging' : '')}>
                {match(question.type,
                    {[QuestionTypes.open_ended]: () => <TextQuestion {...this.props}/>},
                    <DefaultQuestion {...this.props}/>
                )}
            </div>
        ));
    }
}

interface FormQuestionPlaceHolderProps {
    questionId: number;
    onInsertQuestion: (payload: Actions.InsertQuestionPayload) => void;
    onMoveQuestion: (payload: Actions.MoveQuestionPayload) => void;
    connectDropTarget?: ConnectDropTarget;
    connectDragSource?: ConnectDragSource;
}

const placeHolderDropSpec = {
    drop: (props: FormQuestionPlaceHolderProps, monitor: DropTargetMonitor) => {
        if (monitor.didDrop()) {
            // nested target already handled drop
            return;
        }

        // Obtain the dragged item
        const item = monitor.getItem() as QuestionType;

        props.onInsertQuestion({questionId: item.id, beforeQuestionId: props.questionId});
        props.onInsertQuestion({questionId: -1});
    },
};

@ReactDND.DropTarget(DragNDropTypes.FORM_QUESTION, placeHolderDropSpec, collectDrop)
export class FormQuestionPlaceHolder extends DecoratedLustroComponent<FormQuestionPlaceHolderProps> {
    render() {
        const { connectDropTarget } = this.props;
        return connectDropTarget(
            <div className="lustro-survey-form-question-placeholder lustro-survey-form-question">
                <i>Drag Questions Here...</i>
            </div>
        );
    }
}

export function TextQuestion(props: { lang: string; question: QuestionType; }) {
    const lang = props.lang;
    const question: OpenEndedQuestion = props.question as OpenEndedQuestion;
    const rows = question.num_rows || 2;
    const text = QuestionHelper.findTextTranslation(question, lang).text;
    return (
        <div className="form-group">
            <span className="control-label">{text}</span>
            <textarea type="text" rows={rows} className="form-control" />
        </div>
    );
}

function DefaultQuestion(props: FormQuestionProps) {
    const { question, lang } = props;
    const text = QuestionHelper.findTextTranslation(question, lang).text;
    return (
        <div className="form-group" key={question.id}>
            <span className="control-label">Name: </span>
            <p className="form-control-static">
                {QuestionHelper.findNameTranslation(question, lang).text}
            </p>
            <span className="control-label">Type: </span>
            <p className="form-control-static">
                {question.type}
            </p>
            <span className="control-label">Text: </span>
            <p className="form-control-static">
                {text}
            </p>
        </div>
    );
}

