
import * as _ from 'lodash';

export function replaceAt<T>(list: T[], obj: T, atIndex: number) {
    return [
        ...list.slice(0, atIndex),
        obj,
        ...list.slice(atIndex + 1),
    ];
}

export function findAndReplaceOrAppend<T>(list: T[], obj: T, predicate: (t: T) => boolean): T[] {
    const index = _.findIndex(list, predicate);
    return index !== -1 ? replaceAt(list, obj, index) : [...list, obj];
}

export function findAndReplaceOrPrepend<T>(list: T[], obj: T, predicate: (t: T) => boolean): T[] {
    const index = _.findIndex(list, predicate);
    return index !== -1 ? replaceAt(list, obj, index) : [obj, ...list];
}




