/* tslint:disable:no-unused-variable */
// Required for JSX to compile
import React = require('react');
/* tslint:enable:no-unused-variable */
import {LustroComponent} from './LustroComponent';
import {Tag, LanguageLocale} from '../lib/lustroClientApi/lustroClientApi';
import {TagsState} from '../reducers/ApplicationState';
import {DialogEditTagsState} from '../reducers/ApplicationState';
import {EditTagPayload} from '../actions/Actions';
import {CreateTagPayload} from '../actions/Actions';

enum Key {
    ENTER = 13,
}

interface DialogEditTagsProps {
    displayLang: LanguageLocale;
    selectedAccountId: number;
    show: boolean;
    dialogEditTagsState: DialogEditTagsState;
    tags: TagsState;
    onEditTagsCancel: () => any;
    onCreateNewTag: (payload: CreateTagPayload) => any;
    onEditTag: (tagId) => any;
    onEditTagDone: (editTagPayload: EditTagPayload) => any;
}

export class DialogEditTags extends LustroComponent<DialogEditTagsProps> {
    textInput: any = null;
    newTagTextInput: any = null;

    shouldComponentUpdate(nextProps: DialogEditTagsProps) {
        return ! _.isEqual(this.props, nextProps);
    }

    render() {
        const {
            show,
            onEditTagsCancel,
            onEditTag,
            tags,
            displayLang,
            dialogEditTagsState,
            onCreateNewTag,
            selectedAccountId,
            onEditTagDone } = this.props;

        const readOnly = selectedAccountId === null;
        const onClickStopPropagation = (e) =>  {e.stopPropagation(); };
        const Backdrop = () => (<div className="backdrop" onClick={onEditTagsCancel}></div>);
        const getTagNameTranslation = (tag: Tag) => {
            return _(tag.name_translations).find(tran => tran.language_locale === displayLang)
            || _(tag.name_translations).first()
            || { language_locale: '', text: 'Unknown' };
        };
        const createNewTagPayload = () => ({
            text: this.newTagTextInput.value,
            language_locale: displayLang,
            canonical_language_locale: displayLang,
            visibility: 'visible',
            accountId: selectedAccountId
        });
        const TagName = ({tagId, text, language_locale, editMode}) => {
            if (!editMode) {
                return (<span>{text}<sup>{language_locale}</sup></span>);
            }
            else {
                return (
                <div className="input-group">
                    <input
                        className="form-control"
                        id="tag_name"
                        type="text"
                        autoFocus={true}
                        placeholder="Name"
                        defaultValue={text}
                        onBlur={() => onEditTagDone(null)}
                        onKeyPress={(e) =>
                            e.charCode === Key.ENTER && onEditTagDone(
                                {tagId: tagId, text: this.textInput.value, locale: language_locale}
                            )
                        }
                        ref={(elem) => this.textInput = elem}
                    />
                </div>
                );
            }
        };
        const TagCreate = () => {
            return (
                <div className="input-group">
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Tag Name"
                        ref={(elem) => this.newTagTextInput = elem}
                        onKeyPress={(e) => e.charCode === Key.ENTER && onCreateNewTag(createNewTagPayload())}
                    />
                    <span className="input-group-btn">
                        <button
                            className="btn btn-primary"
                            type="button"
                            title="Create new tag"
                            onClick={() => onCreateNewTag(createNewTagPayload())}
                        >Create</button>
                    </span>
                </div>
            );
        };
        const TagListItem = (tag: Tag) => {
            const tran = getTagNameTranslation(tag);
            return (
                <li key={tag.id}
                    className="list-unstyled"
                    onClick={() => {!readOnly && onEditTag(tag.id);}}>
                    <TagName key={tag.id}
                             tagId={tag.id}
                             text={tran.text}
                             editMode={dialogEditTagsState.editingTagId === tag.id}
                             language_locale={tran.language_locale}/>
                </li>
            );
        };
        const Dialog = () => (
            <div className="modal fade in show" tabIndex={-1} role="dialog" onClick={onEditTagsCancel}>
                <div className="modal-dialog" onClick={onClickStopPropagation}>
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" onClick={onEditTagsCancel}>
                                <span>×</span>
                            </button>
                            <h4 className="modal-title">Tags</h4>
                        </div>
                        <div className="modal-body">
                            <ul>
                                {_.map(tags.items, TagListItem)}
                            </ul>
                            { !readOnly ? TagCreate() : null}
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-default" onClick={onEditTagsCancel}>
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            </div>);
        return show ? (<div><Backdrop/><Dialog/></div>) : (<div/>);
    }
}




